.ico{
	display: inline-block;
	vertical-align: middle;
	background-position: 0 0;
	background-repeat: no-repeat;
	background-size: auto 100%;

	img{
		width: 100%;
		height: 100%;
		display: block;
	}

	&_action{
		@include rem(width, 16);
		@include rem(height, 16);

		&:hover{
			background-position: (100% / 2) 0;
		}

		&:active{
			background-position: 100% 0;
		}

		&.ico{


			&_events{
				background-image: url("/img/ico_action/ico_events.svg");
			}
			&_edits{
				background-image: url("/img/ico_action/ico_edits.svg");
			}
			&_menu{
				background-image: url("/img/ico_action/ico_menu.svg");
			}
			&_exit{
				background-image: url("/img/ico_action/ico_exit.svg");
			}
			&_messenger{
				background-image: url("/img/ico_action/ico_messenger.svg");
			}
			&_open{
				background-image: url("/img/ico_action/ico_open.svg");
			}
			&_back{
				background-image: url("/img/ico_action/ico_back.svg");
			}
		}
	}





	&_set{

		&:hover{
			background-position: 100% 0;
		}

		&.ico{

			&_close{
				@include rem(width, 10);
				@include rem(height, 10);
				background-image: url("/img/ico_settings/ico_close.svg");
			}
			&_open{
				@include rem(width, 10);
				@include rem(height, 10);
				background-image: url("/img/ico_settings/ico_open.svg");
			}
			&_search{
				@include rem(width, 10);
				@include rem(height, 10);
				background-image: url("/img/ico_settings/ico_search.svg");
			}
			&_delete{
				@include rem(width, 16);
				@include rem(height, 16);
				background-image: url("/img/ico_settings/ico_delete.svg");
			}
			&_settings{
				@include rem(width, 16);
				@include rem(height, 16);
				background-image: url("/img/ico_settings/ico_settings.svg");
			}
		}
	}


	&_chat{

		@include rem(width, 14);
		@include rem(height, 14);

		&:hover{
			background-position: 50% 50%;
		}

		&.ico{

			&_attantion{
				@include rem(width, 16);
				background-image: url("/img/ico_chat/ico_attantion.svg");
			}

			&_ban{
				background-image: url("/img/ico_chat/ico_ban.svg");
			}

			&_orden{
				background-image: url("/img/ico_chat/ico_orden.svg");
			}

			&_smile{
				background-image: url("/img/ico_chat/ico_smile.svg");
			}

			&_sword{
				background-image: url("/img/ico_chat/ico_sword.svg");
			}
		}
	}




	&_menu{
		@include rem(width, 14);
		@include rem(height, 14);

		//&:hover{
		//	background-position: 100% 0;
		//}

		&.ico{
			&_back{
				background-image: url("/img/ico_menu/ico-head_menu_back.svg");
			}
			&_rocket{
				@include rem(width, 24);
				background-image: url("/img/ico_menu/ico-head_menu_rocket.svg");
			}
			&_next{
				background-image: url("/img/ico_menu/ico-head_menu_next.svg");
			}
			&_target{
				background-image: url("/img/ico_menu/ico-head_menu_target.svg");
			}
			&_task{
				background-image: url("/img/ico_menu/ico-head_menu_task.svg");
			}
			&_fleet{
				@include rem(width, 10);
				@include rem(height, 10);
				background-image: url("/img/ico_menu/ico-head_menu_fleet.svg");
			}
			&_defense{
				@include rem(width, 10);
				@include rem(height, 10);
				background-image: url("/img/ico_menu/ico-head_menu_defense.svg");
			}
			&_resources{
				@include rem(width, 10);
				@include rem(height, 10);
				background-image: url("/img/ico_menu/ico-head_menu_resources.svg");
			}
			&_factories{
				@include rem(width, 10);
				@include rem(height, 10);
				background-image: url("/img/ico_menu/ico-head_menu_factories.svg");
			}
			&_technology{
				@include rem(width, 10);
				@include rem(height, 10);
				background-image: url("/img/ico_menu/ico-head_menu_technology.svg");
			}
		}
	}



	&_info{
		@include rem(width, 16);
		@include rem(height, 16);
		background-position: 50% 50%;

		&.ico{
			&_location{
				@include rem(height, 19);
				background-image: url("/img/ico_info/ico_location.svg");
			}
			&_connect{
				@include rem(height, 15);
				background-image: url("/img/ico_info/ico_connect.svg");
			}
			&_heart{
				background-image: url("/img/ico_info/ico_heart.svg");
			}
			&_in{
				@include rem(width, 19);
				background-image: url("/img/ico_info/ico_in.svg");
			}
			&_load{
				background-image: url("/img/ico_info/ico_load.svg");
			}
			&_oil{
				background-image: url("/img/ico_info/ico_oil.svg");
			}
			&_out{
				@include rem(width, 19);
				background-image: url("/img/ico_info/ico_out.svg");
			}
			&_shield{
				background-image: url("/img/ico_info/ico_shield.svg");
			}
			&_speed{
				@include rem(height, 15);
				background-image: url("/img/ico_info/ico_speed.svg");
			}
			&_sword{
				background-image: url("/img/ico_info/ico_sword.svg");
			}
			&_time{
				@include rem(width, 19);
				background-image: url("/img/ico_info/ico_time.svg");
			}
			&_watch{
				background-image: url("/img/ico_info/ico_watch.svg");
			}
		}
	}


	&_status{
		@include rem(width, 10);
		@include rem(height, 10);
		background-position: 50% 50%;

		&.ico{
			&_good{
				background-image: url("/img/ico_status/ico-status_good.svg");
			}
			&_bad{
				background-image: url("/img/ico_status/ico-status_bad.svg");
			}
			&_norm{
				background-image: url("/img/ico_status/ico-status_norm.svg");
			}
		}
	}







	&_count{
		background: $clrYellow;
		color: #000;
		@include rem(font-size, 8);
		font-weight: 600;
		@include rem(line-height, 14);
		@include rem(height, 14);
		@include rem(padding, 0, 4);
		@include rem(border-radius, 2);
		font-style: normal;
	}

	&-planet{
		@include rem(width, 20);
		@include rem(height, 20);
	}

	&-rocket{
		@include rem(width, 24);
		@include rem(height, 14);
		background-image: url("/img/ico_rest/ico-rocket_white.svg");

		&_red{
			@include rem(width, 24);
			@include rem(height, 14);
			background-image: url("/img/ico_rest/ico-rocket_red.svg");
		}
	}

	&-sword{
		@include rem(width, 14);
		@include rem(height, 14);
		background-image: url("/img/ico_rest/ico-sword.svg");

		&_red{
			@include rem(width, 14);
			@include rem(height, 14);
			background-image: url("/img/ico_rest/ico-sword_red.svg");
		}
	}

	&-orden{
		@include rem(width, 14);
		@include rem(height, 14);
		background-image: url("/img/ico_rest/ico-orden_white.svg");
	}

	&-alert{
		@include rem(width, 20);
		@include rem(height, 20);
		background-image: url("/img/ico_rest/ico-alert.svg");
	}

	&-cartw{
		@include rem(width, 14);
		@include rem(height, 14);
		background-image: url("/img/ico_rest/ico-cart_white_cart.svg");
	}


	&-resource{
		@include rem(width, 14);
		@include rem(height, 14);

		img{
			width: 100%;
			height: 100%;
			display: block;
		}
	}




	&-destination{
		@include rem(width, 14);
		@include rem(height, 14);
	}
}