
.overflow_hidden {
	overflow: hidden;
}

.tjs-planet{
	position: absolute;
	@include rem(top, 0);
	@include rem(left, 0);
	@include rem(bottom, 0);
	@include rem(right, 0);
	//background: rgba(#f00, 0.3);
	@include user-select();
	opacity: 0;

	@include transition(opacity 0.3s ease);
}

.tooltip{
	position: absolute;
	display: none;
	@include rem(margin-top, -33);
	@include rem(margin-left, 20);
	z-index: 120;
	color: #fff;
	@include rem(width, 250);

	&__inner{
		background: #0b0b0b;
		@include rem(border-radius, 7);
		@include box-shadow(0 0 10px rgba(0,0,0,0.5));
		@include rem(padding, 15, 10, 15, 20);

		&:before{
			content: '';
			position: absolute;
			display: block;
			border: solid transparent;
			@include rem(border-width, 12);

			border-right-color: #0b0b0b;
			@include rem(top, 12);
			@include rem(left, -22);
		}
	}

	&__title{
		@include rem(font-size, 17);
		@include rem(margin-bottom, 10);
	}

	&__text{
		@include rem(font-size, 12);
		@include rem(max-height, 140);

		&-inner{

			@include rem(padding-right, 15);
		}
	}

	&.is-open{
		display: block;
	}

}




.popup{
	display: none;
	color: $clrText;

	.mCSB_container{

	}

	.mCSB_vertical{
		.mCSB{
			&_scrollTools{

				@include rem(right, 1);

				.mCSB_dragger_bar{
					@include rem(width, 5);
					background: rgba($clrText, 0.20);
				}
			}
			&_draggerContainer{
				@include rem(top, 7);
				@include rem(bottom, 7);
			}

		}
	}

	.mCSB_horizontal{
		width: 100%;

		&.mCustomScrollBox{
		}

		.mCSB{

			&_container{
			}

			&_scrollTools{

				@include rem(height, 5);
				@include rem(bottom, 10);
				@include rem(padding, 0, 20);


				.mCSB_dragger_bar{
					@include rem(height, 5);
					background: rgba($clrText, 0.20);
					margin: 0 auto;
				}
			}
			&_draggerContainer{
				@include rem(left, 20);
				@include rem(right, 20);
				top: 0;
			}

			&_draggerRail {
				@include rem(height, 5);
				margin: 0;
			}
		}

	}

	&__inner{
		height: 100%;
		left: 0;
		overflow: auto;
		position: fixed;
		text-align: center;
		top: 0;
		white-space: nowrap;
		width: 100%;
		z-index: 102;

		&:before {
			content: '';
			display: inline-block;
			height: 100%;
			vertical-align: middle;
		}

	}

	&__layout{

		display: inline-block;
		margin: 0 auto;
		padding: 0;
		position: relative;
		vertical-align: middle;
		white-space: normal;
		@include rem(width, 738);
		z-index: 102;
		text-align: left;
		@include rem(font-size, 10);
		border: solid $clrBorderLight;
		@include rem(border-width, 2);
		@include rem(border-radius, 7);
		background: $clrDark;
	}


	&__overlay{
		background-color: rgba(0, 0, 0, 0.7);
		display: block;
		height: 100%;
		left: 0;
		position: fixed;
		top: 0;
		width: 100%;
		z-index: 101;
	}


	&__close{
		@include rem(top, 4);
		@include rem(right, 3);
		position: absolute;
		cursor: pointer;
		@include rem(width, 24);
		@include rem(height, 26);
		background: none;
		z-index: 2;

		&:after{
			content: '';
			display: block;
			@include rem(width, 10);
			@include rem(height, 10);
			position: absolute;
			top: 50%;
			left: 50%;
			@include rem(margin-top, -5);
			@include rem(margin-left, -5);
			background-position: 0 0;
			background-repeat: no-repeat;
			background-size: auto 100%;
			background-image: url("/img/ico_settings/ico_close.svg");
		}

		&:hover{

			&:after{
				background-position: 100% 0;
			}
		}

	}

	&__title{
		@include rem(height, 38);
		@include rem(line-height, 38);
		@include rem(font-size, 14);
		font-weight: 500;
		text-align: center;
		@include rem(padding, 0, 40);
		color: #fff;
		border-bottom: solid $clrBorderLight;
		@include rem(border-width, 1);
	}

	&__head{

		position: relative;
		z-index: 1;


		&-menu{
			font-size: 0;
			text-align: center;
			@include rem(padding, 12);

			&-group{
				display: inline-block;
				vertical-align: middle;
				@include rem(margin, 0, 8);
			}


			&-item{
				display: inline-block;
				@include rem(height, 24);
				@include rem(line-height, 24);
				@include rem(padding, 0, 8);
				background: $clrBorder;
				@include rem(margin, 0, -1);

				border: solid $clrBorderLight;
				@include rem(border-width, 2);

				&:first-child{
					@include rem(border-radius, 5, 0, 0, 5);
				}
				&:last-child{
					@include rem(border-top-right-radius, 5);
					@include rem(border-bottom-right-radius, 5);
				}

				&:hover{
					background: $clrDark;

				}

				&.is-active{
					background: $clrDark;
					cursor: default;

					.ico{
						background-position: 100% 0;
					}
					span{
						color: #fff;
					}
				}

				&.is-disable{
					background: $clrDark;
					cursor: default;

					.ico{
						background-position: 0 0 !important;
						opacity: 0.5;
					}
					span{
						opacity: 0.5;
					}
				}

				.ico{
					@include rem(margin, 0, 2);
				}

				span{
					@include rem(font-size, 12);
					display: inline-block;
					vertical-align: middle;
					white-space: nowrap;
					@include rem(margin, 0, 2);
				}
			}
		}
	}



	&__image{
		@include rem(height, 190);
		@include rem(margin-top, -52);
		font-size: 0;

		&:before{
			content: '';
			height: 100%;
			display: inline-block;
			vertical-align: middle;
		}

		img{
			max-width: 100%;
			max-height: 100%;
			text-align: center;
			display: inline-block;
			vertical-align: middle;
		}
	}


	&__tabs{

		&-menu{
			@include user-select();
			font-size: 0;
			overflow: hidden;

			&-title{
				display: inline-block;
				@include rem(height, 25);
				@include rem(line-height, 25);
				font-weight: 500;
				@include rem(padding, 0, 20);

				span{
					@include rem(font-size, 9);
					display: inline-block;
					vertical-align: middle;
				}
			}

			&-all{
				display: inline-block;
				@include rem(height, 25);
				@include rem(line-height, 25);
				float: right;
				@include rem(padding, 0, 15);
				@include user-select();

				span{
					@include rem(font-size, 9);
					display: inline-block;
					vertical-align: middle;
					@include rem(margin-right, 5);
				}
			}

			&-item{
				position: relative;
				display: inline-block;
				@include rem(height, 25);
				@include rem(line-height, 25);
				@include rem(padding, 0, 4, 0, 9);
				@include rem(margin, 0, 6);
				@include user-select();

				span{
					@include rem(font-size, 10);
					display: inline-block;
					vertical-align: middle;
					cursor: pointer;
					@include rem(margin-right, 7);
				}

				&:hover{
					color: #fff;
				}

				&.is-active{
					background: $clrBorder;
					color: #fff;
					text-shadow: 0 0 0.5rem rgba(#fff, 0.5);


					&:before,
					&:after{
						content: '';
						display: block;
						@include rem(width, 20);
						@include rem(height, 26);
						position: absolute;
						background: no-repeat 0 0;
						background-size: contain;
						top: 0;
					}

					&:before{
						background-image: url("/img/tab_left.svg");
						@include rem(left, -20);
					}

					&:after{
						background-image: url("/img/tab_right.svg");
						@include rem(right, -20);
					}
				}

			}
		}

		&-content{

			@include rem(border-radius, 5);
			background: $clrBorder;
			@include rem(height, 180);

			&-item{
				@include rem(padding, 8, 15, 25);
			}

			.mCSB_horizontal{

				&.mCustomScrollBox{
					//overflow: visible;
					@include rem(padding-bottom, 100);
					@include rem(margin-bottom, -100);
				}

				.mCSB{

					&_container{
						overflow: visible !important;
					}

					&_scrollTools{

						@include rem(bottom, 106);

						.mCSB_dragger_bar{

						}
					}
					&_draggerContainer{

					}

					&_draggerRail {

					}
				}

			}

		}
	}

	&__content{

		@include clearfix();
		position: relative;
		color: $colorText;

	}


	&__footer{
		border-top: solid $clrBorderLight;
		@include rem(border-width, 1);
		color: $clrGrey;

		&-left{
			@include rem(height, 30);
			width: calc(50% - 89px);
			width: calc(50% - 5.5625rem);
			@include box-sizing();
			float: left;
			@include rem(padding, 0, 18);

			.btn{
				@include rem(line-height, 30);
				cursor: pointer;

				&:hover{
					.ico{
						background-position: 50% 0;
					}
				}

				&:active{
					.ico{
						background-position: 100% 0;
					}
				}

				.ico{
					@include rem(margin-right, 5);
				}
			}
		}
		&-middle{
			@include rem(width, 178);
			@include rem(height, 30);
			@include box-sizing();
			float: left;

			.btn{
				@include rem(line-height, 30);
				@include rem(height, 30);

				background: $clrRed;
				border: solid $clrRedDark;
				@include rem(border-width, 2);
				@include rem(border-radius, 7);
				text-align: center;
				@include rem(line-height, 30);
				color: #fff;
				@include rem(font-size, 11);
				cursor: pointer;

				width: 100%;
				@include rem(margin, -2);

				&:hover{
					background: $colorRedLight;
				}
				&:active{
					border-color: $clrRed;
				}

				&.is-disable{
					background: none!important;
					color: $clrBorder!important;
					border-color: transparent!important;
					cursor: default!important;
				}
			}
		}
		&-right{
			float: left;
			@include rem(height, 30);
			width: calc(50% - 89px);
			width: calc(50% - 5.5625rem);
			@include box-sizing();
			position: relative;
			@include rem(line-height, 30);
			@include rem(padding, 0, 40,0 , 5);
			text-align: center;
			@include rem(font-size, 10);
		}
		&-donut{
			text-align: right;
			@include rem(padding, 0, 5);

			.donut{
				@include rem(margin-left, 5);
				display: inline-block;
			}
			.ico{
				@include rem(margin, 0, 3);
			}
		}
		&-buy{
			@include rem(height, 30);
			@include rem(width, 33);
			background: $clrViolet;
			border: solid $clrVioletLight;
			@include rem(border-width, 2);
			@include rem(border-radius, 7, 0, 7, 0);
			text-align: center;
			@include rem(line-height, 30);

			position: absolute;
			@include rem(right, -2);
			@include rem(bottom, -2);

			&:hover{
				background: $clrVioletLight;
			}
			&:active{
				border-color: $clrViolet;
			}

			.ico{
				opacity: 0.5;
			}
		}
	}





	// DAR
	&__pp{
		position: absolute;
		top:0;
		left: 0;
		bottom: 0;
		right: 0;
		background: rgba(0,0,0,0.5);
		@include rem(border-radius, 0, 0, 20, 20);
		z-index: 5;
		font-size: 0;
		text-align: center;
		display: none;

		&.is-open{
			display: block;
		}

		&:before{
			content: '';
			display: inline-block;
			vertical-align: middle;
			height: 100%;
		}


		&-layout{
			display: inline-block;
			position: relative;
			vertical-align: middle;
			@include rem(width, 785);
			@include rem(border-radius, 10);
			text-align: left;
			background: #34373e;
			@include rem(min-height, 310);
			@include rem(margin-bottom, 60);
			@include rem(font-size, 15);

		}

		&-close{
			@include rem(width, 28);
			@include rem(height, 28);
			position: absolute;
			cursor: pointer;
			@include rem(top, 20);
			@include rem(right, 20);
			background: url("/img/ico/ico-close.svg") no-repeat 50% 50%;
			background-size: contain;
			opacity: 0.3;
			z-index: 1;

			&:hover{
				opacity: 1;
			}
		}

		&-preview{
			@include rem(width, 345);
			position: absolute;
			top:0;
			left: 0;
			bottom: 0;
			background: #4d525f;
			@include rem(border-radius, 10, 0, 0, 10);
		}

		&-control{
			position: absolute;
			@include rem(bottom, 20);
			@include rem(left, 20);
			@include rem(right, 20);

			.btn{
				&:nth-of-type(2n){
					float: right;
				}

				&.is-disable{
					border-color: #535662!important;
					color: #535662!important;
					cursor: default!important;
					position: relative;
					@include rem(padding-left, 40);
					background: none;

					&:before{
						content: '';
						display: block;
						position: absolute;
						@include rem(width, 16);
						@include rem(height, 16);
						background: url("/img/ico/ico-lock.svg") no-repeat 50% 50%;
						background-size: contain;
						@include rem(top, 16);
						@include rem(left, 16);
					}
				}
			}
		}

		&-content{
			@include rem(margin-left, 345);
			@include rem(padding, 20, 25);
			@include box-sizing();
			position: relative;
			@include rem(min-height, 310);


			&__title{
				@include rem(font-size, 21);
				color: #fff;
				@include rem(margin-bottom, 20);
				@include rem(margin-right, 40);
			}

			&__text{
				@include rem(margin, 15, 0);
				color: $colorText;
			}



		}

		.btn{

			&_big{
				@include rem(height, 48);
				@include rem(padding, 0, 24);
				@include rem(border-radius, 10);
				color: $colorText;
				@include rem(font-size, 21);
				font-weight: 700;
				@include rem(line-height, 48);
				border: solid transparent;
				@include rem(border-width, 2);
				cursor: pointer;

				&:hover{
					color: #fff;
				}
			}

			&_dark{
				background: #2a2d34;
			}

			&_red{
				background: $colorRed;
				color: #fff;
			}

			&_border{
				border-color: $colorText;
			}
		}
	}
}



.counter{

	font-size: 0;
	position: relative;
	@include rem(padding, 0, 22);
	box-sizing: border-box;

	&__value{
		@include rem(height, 25);
		@include rem(font-size, 12);
		font-weight: 600;
		@include rem(line-height, 25);
		text-align: center;
		cursor: default;
		border: solid $clrBorderLight;
		@include rem(border-width, 2);

		&.is-over{
			color: $colorRedLight;
			border-color: $clrRedDark;
		}
	}

	&__arrow{

		@include rem(height, 25);
		@include rem(width, 20);
		position: absolute;
		top: 0;
		cursor: pointer;
		border: solid $clrBorderLight;
		@include rem(border-width, 2);
		background: $clrBorder;
		z-index: 1;

		&.is-disable{
			background: none;
			cursor: default;

			&:before,
			&:after{
				background: $clrBorderLight !important;
			}
		}

		&.is-over{
			background: none;
			cursor: default;
			border-color: $clrRedDark;

			&:before,
			&:after{
				background: $clrRedDark !important;
			}
		}

		&:before,
		&:after{
			content: '';
			@include rem(width, 8);
			@include rem(height, 2);
			position: absolute;
			top:50%;
			left: 50%;
			@include rem(margin-left, -4);
			@include rem(margin-top, -1);
			background: $clrGrey;
			@include rem(border-radius, 10);
		}

		&:hover{
			&:after,
			&:before{
				background: #fff;
			}
		}

		&_down{
			left: 0;
			@include rem(border-radius, 7, 0, 0, 7);
		}
		&_up{
			right: 0;
			@include rem(border-radius, 0, 7, 7, 0);

			&:after{
				@include transform(rotate(90deg));
			}
		}
	}

}

.popup_ship{


	&.popup_compact{

		.popup__layout{
			@include rem(width, 178);
		}

		.popup{
			&__content{
				&-info{
					width: auto;
					float: none;
					padding: 0;

					&-title{
						text-align: center;
					}

					&-list{
						width: auto;
					}
				}

				&-grid{
					width: auto;
					float: none;
				}
			}

			&__footer{
				&-middle{
					.btn{
						@include rem(border-radius, 0, 0, 7, 7);
					}
				}
			}
		}

	}

	.popup{


		&__layout{
			@include rem(width, 518);


		}

		&__close{
			@include rem(top, 0);
			@include rem(right, 0);
		}

		&__header{
			text-align: center;
			@include rem(padding-top, 10);
			line-height: 1.35;
			@include rem(margin-bottom, 5);

			&-subtitle{
				@include rem(font-size, 10);
				color: $clrGrey;
			}
			&-title{
				@include rem(font-size, 14);
				color: #fff;
				@include rem(padding, 0, 10);
			}
		}

		&__content{
			@include rem(padding, 0, 18, 8);
			overflow: hidden;

			&-text{
				float: left;
				@include rem(width, 130);
				@include rem(line-height, 15);

				p{
					@include rem(font-size, 9);
					color: $colorText;
					//font-weight: 300;
					@include rem(margin-bottom, 5);
				}

				ul{
					@include rem(font-size, 10);
					font-weight: 400;
					@include rem(margin-bottom, 5);
				}

				a{
					color: $clrGrey;

					&:hover{

						color: #fff;
						text-decoration: underline;
					}
				}
			}

			&-grid{
				float: left;
				@include rem(width, 215);
				@include rem(min-height, 165);
				position: relative;
				@include rem(margin-bottom, 12);
				font-size: 0;
				text-align: center;

				img{
					max-width: 100%;
					max-height: 100%;
					display: inline-block;
					vertical-align: middle;
				}

				&:after{
					content: '';
					display: inline-block;
					height: 100%;
					vertical-align: middle;
				}

				&:before{
					content: '';
					display: block;
					position: absolute;
					left: 50%;
					bottom: 0;
					@include rem(margin-left, -123);
					@include rem(width, 245);
					@include rem(height, 125);
					background: url("/img/ship_grid.svg") 50% 50% no-repeat;
					background-size: contain;
				}
			}

			&-info{
				float: right;
				@include rem(width, 135);
				@include rem(padding-left, 50);
				box-sizing: border-box;
				@include rem(font-size, 9);

				&-title{
					@include rem(font-size, 9);
					color: $clrOrange;
					@include rem(line-height, 15);
					@include rem(margin-bottom, 3);
				}

				&-text{}

				&-list{
					@include rem(width, 100);
					@include rem(margin-top, 15);
				}

				&-item{
					display: inline-block;
					@include rem(width, 45);
					@include rem(margin-bottom, 7);

					.ico{
						@include rem(width, 20);
					}
					.val{
						display: inline-block;
						vertical-align: middle;
					}
				}
			}

			&-tax{
				float: left;
				width: 100%;
				font-size: 0;
				white-space: nowrap;

				& > *{
					white-space: normal;
				}

				&-title{
					@include rem(font-size, 8);
					text-align: center;
					@include rem(margin-bottom, 3);
				}


				&-counter{
					@include rem(width, 84);
					@include pxToRem(font-size, 10);
					display: inline-block;
					vertical-align: top;
				}

				&-resourses{
					@include rem(width, 300);
					@include rem(margin, 0, 7);
