.chat{

	&-users{

		border: solid $clrBorderLight;
		@include rem(border-width, 2);
		@include rem(border-radius, 7);
		background: $clrDark;

		&__head{
			@include rem(padding, 7, 12, 16, 20);
		}


		&__menu{
			@include rem(height, 25);
			//overflow: hidden;
			border-bottom: solid $clrBorderLight;
			@include rem(border-width, 1);
		}


		&__tab{
			@include rem(height, 25);
			@include rem(line-height, 24);
			@include rem(padding, 0, 15);
			@include rem(margin-left, -1);
			@include rem(margin-bottom, -1);
			background: $clrDark;
			border: solid $clrBorderLight;
			border-bottom: 0;
			@include rem(border-width, 1);
			@include rem(border-radius, 5, 10, 0, 0);
			float: left;
			position: relative;

			&:after{
				content: '';
				display: block;
				@include rem(width, 26);
				@include rem(height, 26);
				background: url("/img/tab_tab.svg") no-repeat 0 0;
				background-size: contain;
				position: absolute;
				@include rem(top, -1);
				@include rem(right, -20);
			}
		}

		&__filter{
			float: right;
			@include rem(line-height, 24);

			.ico{
				@include rem(margin-right, 5);
				opacity: 0.2;
			}
		}


		&__list{
			@include rem(height, 324);
			overflow: hidden;
		}


		&__item{
			@include rem(padding, 0, 35, 0, 30);
			position: relative;
			@include rem(height, 40);
			@include rem(line-height, 40);

			border-top: solid $clrBorderLight;
			@include rem(border-width, 1);
			cursor: default;

			&:first-child{
				border: none;
			}

			&:hover{
				background: #1c1c21;

				.chat-users__item-name,
				.chat-users__item-remove{
					opacity: 1;
				}

				.chat-users__item-filter{
					opacity: 0.7;
				}

			}

			&:active{

			}

			&.is-new{
				border-left: solid $clrBlue;
				@include rem(border-left-width, 2);
			}

			&-name{
				@include rem(font-size, 11);
				display: inline-block;
				@include rem(max-width, 125);
				overflow: hidden;
				white-space: nowrap;
				text-overflow: ellipsis;
				vertical-align: middle;
				opacity: 0.5;
				color: #fff;
			}

			&-filter{
				@include rem(margin-left, 10);
				opacity: 0.2;
			}

			&-state{
				position: absolute;
				@include rem(left, 12);
				@include rem(top, 16);
			}

			&-remove{
				position: absolute;
				@include rem(right, 20);
				@include rem(top, 15);
				opacity: 0;
				cursor: pointer;
			}

			&-add{
				position: absolute;
				@include rem(right, 20);
				@include rem(top, 15);
				@include rem(width, 14);
				@include rem(height, 14);
				cursor: pointer;
				background: url("/img/ico_rest/add_friend.svg") no-repeat 50% 50%;
			}
		}


		&__search{
			position: relative;

			input{
				background: transparent;
				font: inherit;
				width: 100%;
				@include box-sizing();
				@include rem(height, 28);
				@include rem(line-height, 24);
				border: $clrBorder solid;
				@include rem(border-width, 2);
				@include rem(border-radius, 7);
				@include rem(padding, 0, 35, 0, 10);
			}


			&-submit{
				position: absolute;
				top: 0;
				right: 0;
				@include rem(width, 25);
				@include rem(height, 28);
				border-left: $clrBorder solid;
				@include rem(border-width, 1);
				text-align: center;
				@include rem(line-height, 28);
				font-size: 0;
				cursor: pointer;

				&:hover{

					.ico{
						background-position: 100% 0;
					}
				}

			}

		}

	}

	&-forum{

		border: solid $clrBorderLight;
		@include rem(border-width, 2);
		@include rem(border-radius, 7);
		background: $clrBorder;
		color: $clrGrey;

		&__list{
			height: 100%;
			overflow: hidden;
			@include rem(margin-right, -2);
		}


		&__item{

			@include rem(padding, 10, 18);
			border-top: solid $clrBorderLight;
			@include rem(border-width, 1);
			cursor: pointer;
			position: relative;

			&:first-child{
				border: none;
			}

			&:hover{
				background: #2a2c35;

				.chat-forum__item-remove{
					opacity: .2;
				}
			}

			&:active{
				background: #1c1c21;
			}

			&.is-active{
				background: #1c1c21!important;
				cursor: default;
				position: relative;

				&:after{
					content: '';
					position: absolute;
					top: 0;
					right: 0;
					@include rem(width, 2);
					height: 100%;
					background: #e94e3b; /* Old browsers */
					background: -moz-linear-gradient(top, #e94e3b 0%, #f08339 25%, #e32733 100%); /* FF3.6-15 */
					background: -webkit-linear-gradient(top, #e94e3b 0%,#f08339 25%,#e32733 100%); /* Chrome10-25,Safari5.1-6 */
					background: linear-gradient(to bottom, #e94e3b 0%,#f08339 25%,#e32733 100%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
				}
			}

			&-head{

				@include rem(margin-bottom, 14);
				overflow: hidden;
				white-space: nowrap;
				text-overflow: ellipsis;

				.chat-forum__item-filter{
					@include rem(margin-right, 5);
				}

				.chat-forum__item-name{
					display: inline-block;
					vertical-align: middle;
					margin: 0;
				}
			}


			&-remove{
				position: absolute;
				@include rem(right, 18);
				@include rem(top, 9);
				opacity: 0;
				cursor: pointer;
				background-position: 100% 0;

				&:hover{
					opacity: 0.5!important;
				}
			}

			&-name{
				font-weight: 600;
				color: #fff;
				@include rem(font-size, 11);
				@include rem(margin-bottom, 3);

				.date{
					@include rem(font-size, 10);
					font-weight: 400;
					color: $clrGrey;
					@include rem(margin-left, 3);
				}


				&.is-online{
					position: relative;

					&:before{
						content: '';
						position: absolute;
						@include rem(width, 3);
						@include rem(height, 3);
						border-radius: 50%;
						background: $clrGreen;
						@include rem(left, -10);
						@include rem(top, 5);
					}
				}
			}
			&-flex{
				display: -webkit-box;
				display: -moz-box;
				display: -ms-flexbox;
				display: -webkit-flex;
				display: flex;

				justify-content: space-between;
			}
			&-text{
				overflow: hidden;
				white-space: nowrap;
				text-overflow: ellipsis;
			}
			&-count{
				@include rem(padding-left, 5);

				span{
					@include rem(height, 14);
					@include rem(font-size, 8);
					@include rem(line-height, 14);
					background: $clrYellow;
					color: $clrDark;
					text-align: center;
					@include rem(border-radius, 2);
					@include rem(padding, 0, 5);
					display: inline-block;
				}
			}
		}


	}

	&-content{
		border: solid $clrBorderLight;
		@include rem(border-width, 2);
		@include rem(border-radius, 7);
		background: $clrDark;


		&__head{
			@include rem(height, 34);
			border-bottom: solid #000;
			@include rem(border-width, 1);
			@include rem(padding, 0, 20);

			&-title{
				@include rem(font-size, 14);
				color: #fff;
				font-weight: 600;
				@include rem(line-height, 34);
				display: inline-block;
			}

			&-menu{
				display: inline-block;
				position: relative;

				&.is-open{
					.chat-content__head-handle{
						opacity: 1;
					}

					.chat-content__head-list{
						display: block;
					}
				}
			}

			&-handle{
				@include rem(width, 5);
				@include rem(height, 10);
				display: inline-block;
				background: url("/img/ico_rest/ico_menu.svg") no-repeat 50% 50%;
				background-size: contain;
				opacity: 0.4;
				cursor: pointer;
				@include rem(padding, 0, 10);

				&:hover{
					opacity: 1;
				}
			}

			&-list{
				position: absolute;
				@include rem(top, -9);
				@include rem(left, 25);
				border: solid $clrBorderLight;
				@include rem(border-width, 2);
				@include rem(border-radius, 7);
				background: $clrBorder;
				z-index: 10;
				display: none;

				&:before,
				&:after{
					content: '';
					border: solid transparent;
					@include rem(border-width, 8);

					position: absolute;
					@include rem(top, 5);
					border-left-width: 0;
				}

				&:before{
					border-right-color: $clrBorderLight;
					@include rem(left, -8);
				}

				&:after{
					border-right-color: $clrBorder;
					@include rem(left, -5);
				}


			}

			&-item{
				border-top: solid $clrBorderLight;
				@include rem(border-width, 1);
				display: block;
				cursor: pointer;
				@include rem(padding, 6, 10);
				@include rem(width, 100);

				&:hover{
					color: #fff;
				}

				&:first-child{
					border: 0;
				}
			}
		}

		&__list{
			@include rem(height, 322);
			overflow: hidden;
			color: $clrGrey;



			position: relative;

			&:after,
			&:before{
				content: '';
				position: absolute;
				display: block;
				width: 100%;
				height: 0;
			}



			&-inner{
				@include rem(padding, 30, 0);
			}
		}

		&__item{

			border-style: solid;
			@include rem(border-width, 1);
			@include rem(border-radius, 7);
			position: relative;
			@include rem(padding, 9, 30, 9, 9);
			background: #1c1c21;
			@include rem(max-width, 175);
			@include rem(margin, 5, 30);
			clear: both;
			@include rem(min-height, 25);

			&:hover{
				.chat-content__item-remove{
					opacity: 1;
				}
				.chat-content__item-attention{
					opacity: 0.2;
				}
			}

			&:before{
				content: '';
				display: block;
				@include rem(width, 9);
				@include rem(height, 30);
				position: absolute;
				@include rem(top, 7);
				background: none 0 0;
				background-size: contain;
			}

			&_l{
				border-color: transparent;
				float: left;

				&:before{
					background-image: url("/img/chat_arrow_l.svg");
					@include rem(left, -9);
				}
			}

			&_r{
				border-color: #161616;
				float: right;
				background: #000;

				&:before{
					background-image: url("/img/chat_arrow_r.svg");
					@include rem(right, -9);
				}
			}

			&-attention{
				position: absolute;
				@include rem(right, 7);
				@include rem(top, 9);
				opacity: 0;
			}

			&-remove{
				position: absolute;
				@include rem(right, 7);
				@include rem(top, 9);
				opacity: 0;
				cursor: pointer;
			}

			&-name{
				font-weight: 600;
				color: #fff;
				@include rem(font-size, 11);
				@include rem(margin-bottom, 5);

				.date{
					@include rem(font-size, 10);
					font-weight: 400;
					@include rem(margin-left, 3);
					color: $clrGrey;
				}
			}
		}

		&__date{
			float: left;
			width: 100%;
			text-align: center;
			color: $clrBorderLight;
			position: relative;
			@include rem(margin, 8, 0, 12);

			&:before{
				content: '';
				display: block;
				position: absolute;
				top: 50%;
				left: 0;
				@include rem(height, 1);
				width: 100%;
				background: $clrBorderLight;
			}

			span{
				display: inline-block;
				background: 0;
				background: $clrDark;
				position: relative;
				@include rem(padding, 0, 5);
			}
		}

		&__footer{
			@include rem(padding, 7, 16);
			border-top: $clrBorder solid;
			@include rem(border-width, 1);
			@include rem(height, 28);
		}

		&__message{
			position: relative;

			input{
				background: transparent;
				font: inherit;
				width: 100%;
				@include box-sizing();
				@include rem(height, 28);
				@include rem(line-height, 24);
				border: $clrBorder solid;
				@include rem(border-width, 2);
				@include rem(border-radius, 7);
				@include rem(padding, 0, 35, 0, 10);
			}


			&-submit{
				position: absolute;
				top: 0;
				right: 0;
				@include rem(width, 25);
				@include rem(height, 28);
				border-left: $clrBorder solid;
				@include rem(border-width, 1);
				text-align: center;
				@include rem(line-height, 28);
				font-size: 0;
				cursor: pointer;

				&:hover{

					.ico{
						background-position: 100% 0;
					}
				}

			}

		}
	}
}






.popup_fleet{

	.popup{

		&__head{
			//height: 0;
		}

		&__content{
			white-space: nowrap;
			font-size: 0;

			& > *{
				white-space: normal;
				@include rem(font-size, 10);
			}
		}

	}


	.storage{

		&__list{

		}
		&__card{

			&-inner{
			}

			&-title{
				max-height: 47px;
				overflow: hidden;
			}

			&-tank{
				@include rem(margin, 8, 6);
				overflow: hidden;

				label{
					float: left;
				}

				.checkbox{
					@include rem(margin-right, 5);
				}

				.storage__card-clear{
					float: right;
					cursor: pointer;
				}
			}

			&-counter{
				display: none;
				@include rem(margin-top, 10);
			}

			&:hover{
				position: relative;
				z-index: 10;

				.storage__card-inner{
					background: $clrDark;
					@include box-shadow(0 0 1rem rgba(0,0,0,0.75));
				}

				.storage__card-counter{
					display: block;
				}
			}
		}
	}


	&-arrival{
		width: 30%;
		display: inline-block;
		vertical-align: top;
		@include box-sizing();
		text-align: right;
		@include rem(padding, 20, 20, 20, 15);


		.form{
			@include rem(margin-bottom, 13);
			@include rem(width, 145);
			display: inline-block;

		}

		.popup_fleet-info{
			.ico{
				@include rem(margin-left, 5);
			}
		}
	}

	&-path{
		width: 40%;
		display: inline-block;
		vertical-align: top;
		@include box-sizing();
		@include rem(padding, 0, 18);
	}


	&-delivery{
		width: 30%;
		display: inline-block;
		vertical-align: top;
		@include box-sizing();
		@include rem(padding, 20, 15, 20, 20);

		.form{
			@include rem(width, 145);
			@include rem(margin-bottom, 13);
			display: inline-block;
		}

		.popup_fleet-info{
			.ico{
				@include rem(margin-right, 5);
			}
		}
	}


	&-title{
		@include rem(font-size, 14);
		font-weight: 600;
		color: #fff;
		@include rem(margin-bottom, 11);
	}

	&-subtitle{
		color: $clrRed;
		@include rem(margin-bottom, 18);
		font-size: 0;

		span{
			display: inline-block;
			vertical-align: middle;
			@include rem(font-size, 10);
			@include rem(margin, 0, 8);
		}

		.ico{
		}
	}

	&-info{
		@include rem(font-size, 9);
		@include rem(margin-bottom, 13);

		span{
			display: inline-block;
			vertical-align: middle;
		}

		.ico{
			@include rem(width, 20);
		}
	}


	&-planets{

		overflow: hidden;
		@include rem(margin, 75, 0, 35);
		position: relative;

		&-out{
			width: 75px;
			height: 75px;
			padding: 5px;
			border: solid $clrBorderLight;
			@include rem(border-width, 2);
			border-radius: 50%;
			@include box-sizing();
			float: left;

			img{
				width: 100%;
				height: 100%;
				display: block;
			}
		}
		&-arrow{
			position: absolute;
			@include rem(height, 2);
			background: $clrBorderLight;
			top: 37px;
			left: 75px;
			right: 75px;
			@include rem(border-radius, 100);

			&:before,
			&:after{
				content: '';
				display: block;
				@include rem(border-radius, 100);
				@include rem(height, 2);
				@include rem(width, 6);
				background: $clrBorderLight;
				position: absolute;
				right: 0;
				top: 50%;
			}

			&:before{
				@include transform(rotate(40deg));
				@include rem(margin-top, -4);

			}

			&:after{
				@include transform(rotate(-40deg));
				@include rem(margin-top, 2);
			}
		}

		&-in{
			width: 75px;
			height: 75px;
			padding: 5px;
			border: solid $clrBorderLight;
			@include rem(border-width, 2);
			border-radius: 50%;
			@include box-sizing();
			float: right;

			img{
				width: 100%;
				height: 100%;
				display: block;
			}
		}
	}


	&-range{

		@include rem(width, 220);
		margin: 0 auto;

		.range{
			position: relative;

			&__counter{
				@include rem(width, 105);
				margin: 0 auto;
				@include rem(margin-bottom, 12);

				.counter{
					&__value{
						&:after{
							content: '%';
						}
					}
				}

			}

			&__slider{
				color: $colorYellow;
			}

			&__current{
				position: absolute;
