					}

					&-counter{
						font-size: 0;
						position: absolute;
						@include rem(bottom, 2);
						@include rem(left, 2);
						@include rem(right, 2);

						&-value{
							@include rem(height, 30);
							float: left;
							@include rem(font-size, 17);
							@include rem(line-height, 30);
