							&:before{
								content: '';
								@include rem(width, 13);
								@include rem(height, 3);
								position: absolute;
								top: 50%;
								left: 50%;
								@include rem(margin-left, -6);
								@include rem(margin-top, -1);
								background: #757782;
								@include rem(border-radius, 10);
							}
						}
						&-up{
							@include rem(height, 30);
							@include rem(width, 30);
							background: #40434c;
							float: left;
							position: relative;
							cursor: pointer;
							@include rem(border-radius, 0, 0, 3, 0);

							&:before,
							&:after{
								content: '';
								@include rem(width, 13);
								@include rem(height, 3);
								position: absolute;
								top:50%;
								left: 50%;
								@include rem(margin-left, -6);
								@include rem(margin-top, -1);
								background: #757782;
								@include rem(border-radius, 10);
							}

							&:after{
