					}

					&-item{
						@include rem(width, 290);
						float: left;
						text-align: center;
					}

					&-title{
						@include rem(font-size, 19);
						@include rem(margin, 20, 0);
						color: #fff;
					}

					&-view{
						@include rem(margin, 20, 0);
						position: relative;

						span{
							display: block;
							margin: 0 auto;
							@include rem(width, 146);
							@include rem(height, 146);
							border: solid #3d4049;
							@include rem(border-width, 3);
							@include box-shadow(inset 0 0 0 0.187rem #2b2d34);
							border-radius: 50%;
							background: #27292e;

						}
					}

					&-text{
						@include rem(font-size, 14);
						@include rem(margin, 10, 0);
					}

					&-planet{

						@include rem(margin, 15, 0, 25);
						&-name{

						}
					}

					&-coordinates{
						text-align: center;

						&-item{
							border: solid #434753;
							@include rem(border-width, 2);
							color: #fff;
							@include rem(height, 35);
							@include rem(line-height, 35);
							display: inline-block;
							vertical-align: middle;
							@include rem(width, 50);
							@include rem(border-radius, 5);
							background: none;
							text-align: center;

