						&__slider{
						}

						&__current{
							position: absolute;
							left: 0;
							@include rem(top, -20);
							color: currentColor;
						}

						&__max{
							position: absolute;
							right: 0;
							@include rem(top, -20);
							color: $colorText;
						}

						.noUi{
							color: inherit;

							&-horizontal{
								@include rem(height, 12);
							}

