								@include rem(border-width, 4);
								@include rem(left, -11);
								@include rem(top, -6);
							}

							&-target{
								@include rem(border-radius, 100);
								@include rem(padding, 0, 8);
							}

							&-connect{
								background: currentColor;
								@include rem(border-radius, 100, 0, 0, 100);
								@include rem(margin-left, -8);
							}
						}
					}
				}




				&__target{
					@include rem(padding, 20, 40);
					border-bottom: solid #353840;
					@include rem(border-bottom-width, 2);
					@include rem(font-size, 14);

					&-title{
						@include rem(font-size, 19);
						color: #fff;
						@include rem(margin-bottom, 20);
					}

					&-list{
						font-size: 0;
						@include rem(margin, 0, -7, 15);
					}

					&-item{
						text-align: center;
						display: inline-block;
						@include rem(margin, 0, 7);

						&-img{
							@include rem(width, 92);
							@include rem(height, 92);
							border: solid #2b2d34;
							@include rem(border-width, 2);
							@include rem(border-radius, 10);
							background: #3c3f47;
							@include box-shadow(inset 0 0 0 0.125rem #484c57);
							@include rem(margin-bottom, 2);

							font-size: 0;

							&:after{
								content: '';
								display: inline-block;
								height: 100%;
								vertical-align: middle;
							}

							span{
								display: inline-block;
								vertical-align: middle;
								width: 100%;
								max-height: 100%;

								img{
									max-width: 100%;
									max-height: 100%;
								}
							}
						}

						&-name{
							@include rem(font-size, 14);
							white-space: nowrap;
							text-overflow: ellipsis;
							overflow: hidden;
						}

						&-input{
							position: absolute;
							opacity: 0;

							&:checked{
								~ .fleet__target-item-img{
									@include box-shadow(inset 0 0 0 0.125rem #21232b);
									border-color: #434650;
									background: #21232b;
								}
							}

							&:disabled{
								~ .fleet__target-item-img{
									@include box-shadow(none);
									border-style: dotted;
									border-color: #444751;
									background: url("/img/ico/ico-lock.svg") no-repeat 50% 50%;
									@include rem(background-size, 20, 20);
								}
							}
						}
					}


					&-info{
						overflow: hidden;
						@include rem(padding, 1, 0);

						&-col{
							float: left;
							@include rem(width, 275);

							&_time{
								@include rem(width, 240);
								@include rem(margin-left, 45);
							}

							&_stat{
								@include rem(font-size, 21);
								text-align: right;
								@include rem(width, 500);
								@include rem(margin-left, 40);

								.btn{
									border-color: #4b4d5a;
									@include rem(height, 36);
									@include rem(line-height, 36);
									@include rem(margin-left, 20);
									@include rem(border-radius, 5);
									&:hover{
										color: #fff;
									}
								}
							}
						}
					}
				}

				&__store{

					@include rem(padding, 0, 40, 20);

					&-table{
						display: table;
						width: 100%;

						@include rem(border-spacing, 0, 22);
					}

					&-row{

						display: table-row;
					}

					&-item{

						display: table-cell;
						vertical-align: middle;
						text-align: right;
						white-space: nowrap;

					}

					&-name{

						display: inline-block;
						vertical-align: middle;

						.ico{
							display: inline-block;
							vertical-align: middle;
							border-radius: 50%;
							@include rem(width, 19);
							@include rem(height, 19);
							background: #41444f;
							@include rem(margin-right, 5);

							img{
								display: block;
								width: 100%;
								height: 100%;
							}
						}

						span{
							display: inline-block;
							vertical-align: middle;
						}
					}

					&-counter{

						display: inline-block;
						vertical-align: middle;

						.counter{
							@include rem(margin-left, 10);

							&__value{
								@include rem(width, 165);
							}
						}
					}



				}
			}
		}
	}




}



