				display: block;
				width: 100%;
				height: 100%;
			}

			@include rem(border-radius, 10);
			border: solid rgba($colorDark, 0.3);
			@include rem(border-width, 1);
			@include rem(padding, 20);
			font-size: 0;
			@include rem(margin, 10, 0);
			position: relative;

			&:hover{
				background: rgba($colorText, 0.15);

				.planet__item-next{
					background: rgba($colorText, 0.15);
				}
			}

			&.is-open{
				.planet__item-all{
					display: block;
				}
				.planet__item-name-title{
					.arrow{
						@include transform(scale(-1));
					}
				}
			}

			&-next{
				@include rem(width, 65);
				top: 0;
				right: 0;
				bottom: 0;
				position: absolute;
				@include rem(border-radius, 0, 10, 10, 0);
				cursor: pointer;

				&:hover{
					background: rgba($colorText, 0.3)!important;

					&:before{
						opacity: 0.5;
					}
				}

				&:before{
					content: '';
