				margin: 0 auto;
				@include rem(margin-top, 15);
			}
		}


		&__content{
			background: #34373e;

			.buy{

				&__list{
					@include rem(min-height, 150);
					font-size: 0;
					text-align: center;
					background: #2f3138;
					overflow: hidden;

					.mCSB_horizontal .mCSB_scrollTools{
						@include rem(bottom, 10);
					}
				}

				&__item{
					text-align: left;
					position: relative;
					display: inline-block;
					@include rem(font-size, 15);
					@include rem(width, 325);
					@include rem(height, 115);
					@include rem(border-radius, 10);
					border: $clrBorderLight solid;
					@include rem(border-width, 2);
					@include rem(margin, 20, 7);
					@include rem(padding, 10, 10);
					@include box-sizing();

					&-remove{
						@include rem(width, 28);
						@include rem(height, 28);
						position: absolute;
						cursor: pointer;
						@include rem(top, 10);
						@include rem(right, 10);
						background: url("/img/ico/ico-close.svg") no-repeat 50% 50%;
						background-size: contain;
						opacity: 0.3;

						&:hover{
							opacity: 1;
						}
					}

					&-logo{
						float: left;
						@include rem(padding, 0);
					}

					&-info{
						@include rem(margin-left, 110);
					}

					&-name{
						@include rem(font-size, 19);
						font-weight: 300;
						color: #fff;
						white-space: nowrap;
						overflow: hidden;
						text-overflow: ellipsis;
						@include rem(margin-bottom, 5);
						@include rem(margin-top, 5);
						@include rem(margin-right, 25);
					}

					&-val{
						@include rem(font-size, 16);
						white-space: nowrap;
						overflow: hidden;
						text-overflow: ellipsis;
						@include rem(margin-bottom, 10);
					}

					&-price{
						@include rem(font-size, 21);
						font-weight: 500;
						color: #fff;
						white-space: nowrap;
						overflow: hidden;
						text-overflow: ellipsis;
					}

				}

				&__pay{
					text-align: center;
					&-result{
						@include rem(font-size, 27);
						color: #fff;
						font-weight: 500;
						@include rem(margin-top, 25);
						@include rem(margin-bottom, 40);
					}
					&-title{
						@include rem(font-size, 16);
						font-weight: 500;
						@include rem(margin-bottom, 5);
					}
					&-list{}

					&-item{
						display: inline-block;
						@include rem(width, 116);
						@include rem(height, 196);
						@include rem(padding, 10);

						@include rem(border-radius, 10);
						background: #3a3d45;
						border: solid $clrBorderLight;
						@include rem(border-width, 2);
						@include box-sizing();
						@include box-shadow(inset 0 0 0 1px #464a56);
						text-align: center;
						@include rem(margin, 5, 4);
						cursor: default;
						font-size: 0;

						&:before{
							content: '';
							height: 100%;
							display: inline-block;
							vertical-align: middle;
						}


						&:hover{
							background: #34373e;
						}
					}
					&-inner{
						@include rem(font-size, 13);
						display: inline-block;
						vertical-align: middle;
					}
					&-logo{
						.ico{
							@include rem(width, 90);
							@include rem(height, 90);
						}
					}
					&-name{}
				}

			}
		}
	}
}

.popup_stat{

	.popup{

		&__head{

			color: $colorText;
			@include rem(padding, 25, 15);

			&-tabl{

			}

			&-col{
				@include rem(padding, 0, 20);
				@include rem(font-size, 14);
				vertical-align: top;
			}

			&-item{
				@include rem(padding, 0, 20);
				@include rem(min-height, 48);
				@include rem(line-height, 48);
				background: #2a2d34;

				@include rem(font-size, 21);
				font-weight: 600;
				display: inline-block;
				vertical-align: middle;
				text-align: center;
				@include rem(border-radius, 10);
				color: #fff;
				@include rem(margin, 0, 12);

				.ico{
					@include rem(height, 20);
					display: inline-block;
					vertical-align: middle;
					opacity: 0.5;
					@include rem(margin-right, 5);
				}

				span{
					display: inline-block;
					vertical-align: middle;
					opacity: 0.5;
				}

				&:hover{
					.ico{
						opacity: 1;
					}

					span{
						opacity: 1;
					}
				}

				&.is-active{
					background: $colorRed;

					.ico{
						opacity: 1;
					}

					span{
						opacity: 1;
					}
				}
			}

		}




	}


	.stat{

		&__head{

			overflow: hidden;
			@include rem(padding, 25, 0);

			&-item{

				float: left;
				@include rem(font-size, 17);
				@include rem(line-height, 21);
				display: block;

				&_1{
					@include rem(margin-left, 125);
					@include rem(width, 300);
				}

				&_2{
					@include rem(width, 165);
				}

				&_3{
					@include rem(width, 245);
				}

				&.is-active{
					@include rem(font-size, 21);
					font-weight: 600;
					color: #fff;
				}
			}
		}


		&__table{
			@include rem(height, 400);
			@include box-shadow(inset 0 -29px 20px -20px rgba(0, 0, 0, 0.35));
		}

		&__list{
			@include rem(padding, 5, 60, 20, 20);
		}

		&__item{

			border: solid #26272d;
			@include rem(border-width, 1);
			@include rem(border-radius, 5);
			display: table;
			width: 100%;
			@include rem(margin-bottom, 10);

			&:hover{
				background: #34373e;
			}

			&-col{
				display: table-cell;
				vertical-align: middle;
				@include rem(padding, 15);
				@include rem(font-size, 19);
				color: #fff;

				.keep__item{
					@include rem(width, 60);
					@include rem(height, 60);
				}

				&_image{
					@include rem(width, 60);
				}

				&_name{
					@include rem(width, 250);
				}

				&_info{
					text-align: center;
					@include rem(width, 200);

					.ico{

						display: inline-block;
						vertical-align: middle;
						@include rem(height, 20);
						opacity: 0.3;
						@include rem(margin-left, 5);

						&:hover{
							opacity: .5;
						}

						img{
							display: block;
							height: 100%;
						}
					}

					.label{
						span{
							@include rem(font-size, 13);
							color: $colorText;
							display: inline-block;
							@include rem(height, 21);
							@include rem(line-height, 21);
							border: solid #41444f;
							@include rem(border-width, 1);
							@include rem(border-radius, 3);
							@include rem(padding, 0, 12);
						}
					}
				}

				&_clear{

					@include rem(width, 80);
					text-align: center;

					.ico{

						@include rem(width, 28);
						@include rem(height, 28);
						cursor: pointer;
						display: inline-block;
						background: url("/img/ico/ico-close.svg") no-repeat 50% 50%;
						background-size: contain;
						opacity: 0.3;

						&:hover{
							opacity: 1;
						}
					}
				}
			}
		}

		&__foot{
			@include rem(padding, 30, 210, 0, 40);
			position: relative;

			&-text{
				text-align: right;

				.val{
					color: #fff;
				}
			}

			&-clear{

				@include rem(width, 28);
				@include rem(height, 28);
				position: absolute;
				cursor: pointer;
				@include rem(top, 25);
				@include rem(right, 100);
				background: url("/img/ico/ico-close.svg") no-repeat 50% 50%;
				background-size: contain;
				opacity: 0.3;

				&:hover{
					opacity: 1;
				}
			}
		}
	}
}