.header{
	background: #000;
	position: relative;
	z-index: 2;
	@include rem(height, 29);
	@include rem(margin-bottom, -29);

	@include transition(left 0.3s ease);
	left: 0;
	font-size: 0;
	color: $clrGrey;

	&__inner{
		width: 100%;
		height: 100%;
	}


	&__line{
		background: $colorBlue;
		@include rem(height, 2);
		@include rem(min-width, 1);
		width: 100%;
		position: absolute;

		background: #e32632;
		background: -moz-linear-gradient(left, #e32632 0%, #e32632 51%, #ff8832 68%, #e32632 87%, #e32632 100%);
		background: -webkit-linear-gradient(left, #e32632 0%,#e32632 51%,#ff8832 68%,#e32632 87%,#e32632 100%);
		background: linear-gradient(to right, #e32632 0%,#e32632 51%,#ff8832 68%,#e32632 87%,#e32632 100%);
	}

	&__item{
		display: inline-block;

		&-val{
			@include rem(line-height, 29);
			white-space: nowrap;
			@include user-select();

			.ico{

				&_count{
					@include rem(margin-left, 5);
				}
				.header__item_treasure &{
					@include rem(width, 14);
					@include rem(height, 14);
				}

			}

			.val{
				display: inline-block;
				vertical-align: middle;
				@include rem(font-size, 10);
				@include rem(margin-left, 3);
			}

			.header__item_menu &{
				@include rem(padding, 0, 15);
				border-right: solid $clrBorder;
				@include rem(border-width, 2);
			}

			.header__item_messenger &{
				@include rem(padding, 0, 10);
			}

			.header__item_events &{
				@include rem(padding, 0, 10);
				border-right: solid $clrBorder;
				@include rem(border-width, 2);
			}

			.header__item_treasure &{
				@include rem(padding, 0, 8);

				&_add{
					height: 18px;
					line-height: 18px;
					border: solid #2a2c33;
					@include rem(border-width, 1);
					@include rem(border-radius, 3);
					padding-right: 0;
					cursor: pointer;

					.val{
						height: 18px;
						line-height: 18px;
					}

					.add{
						display: inline-block;
						height: 18px;
						width: 21px;
						@include pxToRem(margin, -1);
						vertical-align: middle;
						margin-left: 5px;

						background: $clrViolet;
						border: solid $clrVioletLight;
						@include rem(border-width, 1);
						@include rem(border-radius, 3);
						position: relative;


						&:before,
						&:after{
							content: '';
							width: 8px;
							height: 2px;
							position: absolute;
							top: 50%;
							left: 50%;
							margin-top: -1px;
							margin-left: -4px;
							background: $clrVioletLight;
						}

						&:before{
							@include transform(rotate(90deg));
						}
					}

					&:hover:not(:active){
						color: #fff;
						background: rgba(#888eab, 0.2);
					}

					&:active{

						.add{
							background: $clrVioletLight;
							border-color: $clrViolet;
							&:before,
							&:after{
								background: $clrViolet;
							}
						}

					}
				}
			}

			.header__item_planet &{
				@include rem(padding, 0, 10);
				@include rem(font-size, 12);

				&:hover:not(:active){
					background: rgba(#888eab, 0.2);
				}

				&:active{
					color: #fff;
				}
			}

			.header__item_space &{
				@include rem(padding, 0, 10);
				@include rem(font-size, 12);

				&:hover:not(:active){
					background: rgba(#888eab, 0.2);
				}

				&:active{
					color: #fff;
				}
			}


			cursor: pointer;

			&:hover{
				.ico_action{
					background-position: (100% / 2) 0;
				}
			}
			&:active{
				.ico_action{
					background-position: 100% 0;
				}
			}

		}


		&_planet{
			border-right: solid $clrBorder;
			@include rem(border-width, 2);

			.ico_open{
				@include rem(margin-left, 10);
			}
		}

		&_space{
			border-right: solid $clrBorder;
			@include rem(border-width, 2);

			.ico_vortex{
				@include rem(width, 20);
				@include rem(height, 20);
				background-image: url("/img/ico_rest/ico_vortex.svg");
			}

			.ico_open{
				@include rem(margin-left, 10);
			}
		}

	}

	&__place{
		float: right;
		@include rem(margin-right, 45);
		border-left: solid $clrBorder;
		@include rem(border-width, 2);
	}
}