.storage{

	&__table{

		font-size: 0;
		text-align: center;
		@include rem(padding-top, 22);

		&-title{
			@include rem(font-size, 11);
			color: #fff;
			@include rem(margin-bottom, 13);
		}
	}

	&__col{
		display: inline-block;
		@include rem(width, 72);
		@include rem(font-size, 10);

		&-title{
			@include rem(font-size, 11);
		}

		&_middle{
			@include rem(margin, 0, 23);
		}
	}

	&__item{
		margin: 0 auto;
		position: relative;


		.game__planet-storage &{
			@include rem(margin-top, 8);
			@include rem(margin-bottom, 18);
		}

		&.show-plate{
			z-index: 1;
		}

		&-inner{
			@include rem(width, 54);
			@include rem(height, 54);
			margin: 0 auto;
			position: relative;

			//.storage__item_done &{
			//	filter: saturate(75%);
			//}
			//
			//.storage__item_done:hover &{
			//	filter: saturate(100%);
			//}
		}

		&-frame{
			position: absolute;
			width: 100%;
			height: 100%;
			top: 0;
			left: 0;

			svg{
				display: block;
				width: 100%;
				height: 100%;

				path{
					fill-opacity: 0;
				}
			}

		}

		&-progress{
			position: absolute;
			bottom: 0;
			left: 0;
			width: 100%;
			overflow: hidden;

			svg{
				@include rem(width, 54);
				@include rem(height, 54);
				position: absolute;
				bottom: 0;
				left: 0;
			}
		}


		&-ico{
			position: absolute;
			width: 100%;
			height: 100%;
			top: 0;
			left: 0;

			svg{
				display: block;
				width: 100%;
				height: 100%;

				path{
					fill: currentColor;
				}
			}

		}

		&-timer{
			position: absolute;
			text-align: center;
			width: 100%;
			top: 100%;
			left: 0;
			@include rem(margin-top, -4);
			cursor: default;
			@include user-select();

			span{
				background: currentColor;
				display: inline-block;
				@include rem(font-size, 8);
				@include rem(height, 14);
				@include rem(line-height, 14);
				@include rem(padding, 0, 5);
				@include rem(border-radius, 2);
				@include box-shadow(0 0 1rem rgba(0,0,0,1));

				i {
					font: inherit;
					color: #000;
				}
			}
		}



		&-plate{
			position: absolute;
			@include rem(top, -18);
			@include rem(left, -17);
			@include rem(width, 74);
			border: $clrBorderLight solid;
			@include rem(border-width, 2);
			@include rem(border-radius, 7);
			@include rem(padding, 90, 5, 0);
			background: $clrDark;
			display: none;

			.show-plate &{
				display: block;
			}

			&-close{
				position: absolute;
				@include rem(top,7);
				@include rem(right, 7);
				cursor: pointer;
			}

			&-title{
				@include rem(font-size, 10);
				color: #fff;
				@include rem(margin-bottom, 12);
			}

			&-btns{

				@include rem(margin-bottom, 14);

				.btn{
					display: inline-block;
					border: solid $clrBorderLight;
					@include rem(font-size, 10);
					@include rem(border-width, 1);
					@include rem(height, 14);
					@include rem(line-height, 14);
					@include rem(padding, 0, 4);
					vertical-align: middle;

					@include rem(margin, 0, 0, 3);
					@include rem(border-radius, 3);
					font-weight: 600;
					color: $clrGrey;
					cursor: pointer;

					&:hover{
						color: #000;
						background: $clrGrey;
						border-color: $clrGrey;
					}
				}
			}

			&-counter{
				@include rem(margin, 0, -7, -2);
				border-top: $clrBorder solid;
				@include rem(border-width, 2);
				font-size: 0;
				white-space: nowrap;

				.counter{
					&-btn{
						@include rem(margin-top, -2);
						@include rem(width, 24);
						@include rem(height, 24);
						@include rem(line-height, 24);
						display: inline-block;
						vertical-align: middle;
						border: $clrBorderLight solid;
						@include rem(border-width, 2);
						@include rem(border-radius, 5);
						background: $clrBorder;
						@include box-shadow(0 0 0.5rem rgba(0,0,0,0.5));
						position: relative;
						cursor: pointer;

						&:before{
							content: '';
							display: block;
							position: absolute;
							top: 50%;
							left: 50%;
							@include pxToRem(margin-top, -1);
							@include pxToRem(margin-left, -4);
							@include pxToRem(width, 8);
							@include pxToRem(height, 2);
							background: $clrGrey;
						}

						&_dec{
							@include rem(border-radius, 0, 7);
						}

						&_inc{
							@include rem(border-radius, 7, 0);

							&:after{
								content: '';
								display: block;
								position: absolute;
								top: 50%;
								left: 50%;
								@include pxToRem(margin-top, -4);
								@include pxToRem(margin-left, -1);
								@include pxToRem(width, 2);
								@include pxToRem(height, 8);
								background: $clrGrey;
							}
						}

						&:hover{
							background: #2a303f;
							border-color: #343f51;

							&:before,
							&:after{
								background: #fff;
							}
						}
					}

					&-val{
						@include rem(margin-top, -2);
						display: inline-block;
						vertical-align: middle;
						@include rem(height, 24);
						@include rem(width, 32);
						@include rem(line-height, 24);
						@include rem(font-size, 10);
						color: $clrGrey;
					}
				}
			}
		}

	}


	&_ico{


		&-lock{
			display: none;

			.storage__item_lock &{
				display: block;
				fill: #fff;
				fill-opacity: 1;
			}
		}
		&-ready{
			display: none;

			.storage__item_ready &{
				display: block;
				fill: #fff;
				fill-opacity: 1;
			}
		}
		&-inner{
			display: none;

			.storage__item_ready &{
				stroke: #fff;
				stroke-width: 1px;
				display: block;
				fill: #fff;
				fill-opacity: 0.2;
			}



			.storage__item_ready:hover &{
				fill-opacity: 0.4;
			}

		}
		&-frame{
			stroke-width: 2px;
			stroke: currentColor;

			.storage__item_done .storage__item-progress &{
				fill: currentColor;
				fill-opacity: 0.4;
			}


			.storage__item_done .storage__item-frame &{
				fill: currentColor;
				fill-opacity: 0.2;
			}

			.storage__item_done:hover .storage__item-frame &{
				fill: currentColor;
				fill-opacity: 0.3;
			}


			.storage__item_ready &{
				stroke: #fff;
				stroke-dasharray:1,2;
				stroke-width: 1px;
				stroke-opacity: 1;
			}

			.storage__item_ready:hover &{
				animation: dash 70s linear infinite;
			}

			.storage__item_lock &{
				stroke-width: 1px;
				stroke: #fff;
				stroke-dasharray:1,2;
				stroke-opacity: 1;
			}
		}
	}


	&__list{
		font-size: 0;
		white-space: nowrap;

	}

	&__card{
		white-space: normal;
		//background: $clrDark;
		@include rem(font-size, 10);
		display: inline-block;
		vertical-align: top;
		@include rem(height, 145);

		&.is-disable{
			cursor: default;

			.storage__card-inner{

				& > *{
					opacity: 0.3;
				}
			}

		}

		.storage__item{
			@include rem(margin-top, 15);
			@include rem(margin-bottom, 5);
		}

		&-inner{
			border: solid $clrBorderLight;
			@include rem(border-width, 2);
			@include rem(border-radius, 7);
			@include rem(margin, 0, 4);
			@include rem(width, 84);
			min-height: 100%;
			@include box-sizing();
			background: $clrBorder;
			position: relative;
		}

		&-title{
			text-align: center;
			color: #fff;
			@include rem(font-size, 10);
			line-height: 1.5;
			@include rem(margin-bottom, 8);
		}

		&-clear{
			cursor: pointer;
		}

		&-btns{
			text-align: center;

			.btn{
				display: inline-block;
				border: solid $clrBorderLight;
				@include rem(font-size, 10);
				@include rem(border-width, 1);
				@include rem(height, 14);
				@include rem(line-height, 14);
				@include rem(padding, 0, 4);
				vertical-align: middle;

				@include rem(margin, 0, 0, 3);
				@include rem(border-radius, 3);
				font-weight: 600;
				color: $clrGrey;
				cursor: pointer;

				&:hover{
					color: #000;
					background: $clrGrey;
					border-color: $clrGrey;
				}
			}
		}

		&-counter{
			@include rem(margin, 0, -2, -2);

			.counter{

				&__value{
					@include pxToRem(margin, 0, -5);
					border-color: $clrBorder;
					border-right-color: transparent;
					border-left-color: transparent;
				}

				&__arrow{

					&.is-disable{
						border-color: $clrBorder;
					}

					&_down{
						@include rem(border-radius, 0, 7, 0, 7);
					}

					&_up{
						@include rem(border-radius, 7, 0, 7, 0);
					}
				}
			}
		}

	}

}

@keyframes dash {
	to {
		stroke-dashoffset: -1000;
	}
}
