.form{
	@include rem(font-size, 10);

	&__field{
		@include rem(margin, 6, 0);
	}

	&__select,
	&__coordinates,
	&__textarea,
	&__input{
		border: $clrBorderLight solid;
		@include rem(border-width, 2);
		@include rem(border-radius, 7);
		@include rem(height, 24);
		@include rem(line-height, 24);
		position: relative;
	}

	&__textarea{
		height: auto;

		textarea{
			display: block;
			@include pxToRem(padding, 8, 10);
			@include box-sizing();
			@include rem(height, 130);
			resize: none;
			width: 100%;
			background: transparent;
			font: inherit;
			line-height: 1.2;
			@include rem(border-radius, 5);

			@include transition(all $animateTime);
			@include box-shadow(inset 0 0 0 rgba(0,0,0,0.75));
			&:focus{
				background-color: $clrBorderLight;
				color: #fff;
				@include box-shadow(inset 0 0 0.6rem rgba(0,0,0,0.75));
			}
		}
	}

	&__coordinates{

		.form__field_disable &{
			@include rem(border-width, 1);
			border-style: dashed;
		}
	}

	&__input{

		input{
			display: block;
			@include pxToRem(padding, 0, 10);
			@include box-sizing();
			@include rem(line-height, 24);
			@include rem(height, 24);
			width: 100%;
			background: transparent;
			font: inherit;
			@include rem(border-radius, 5);

			@include transition(all $animateTime);
			@include box-shadow(inset 0 0 0 rgba(0,0,0,0.75));
			&:focus{
				background-color: $clrBorderLight;
				color: #fff;
				@include box-shadow(inset 0 0 0.6rem rgba(0,0,0,0.75));
			}

			&:disabled{

			}
		}

		.form__field_disable &{
			@include rem(border-width, 1);
			border-style: dashed;
		}
	}

	&__select{

		input{
			position: absolute;
			opacity: 0;
			width: 1px;
			height: 1px;
		}

		&-value{
			@include pxToRem(padding, 0, 35, 0, 10);
			white-space: nowrap;
			overflow: hidden;
			text-overflow: ellipsis;
			cursor: pointer;
		}

		&-handler{
			position: absolute;
			@include rem(height, 24);
			@include rem(width, 26);
			border: $clrBorderLight solid;
			@include rem(border-width, 2);
			@include rem(border-radius, 0, 7, 7, 0);
			@include rem(top, -2);
			@include rem(right, -2);
			text-align: center;
			cursor: pointer;

			.is-open &,
			&:hover{

				.ico{
					background-position: 100% 0;
				}
			}
		}

		&-list{
			position: absolute;
			border: $clrBorderLight solid;
			@include rem(border-width, 2);
			@include rem(border-radius, 7);
			@include rem(top, 24);
			@include rem(right, -2);
			@include rem(left, -2);
			background: $clrDark;
			z-index: 1;
			overflow: hidden;
			@include rem(max-height, 200);
			display: none;

			.is-open &{
				display: block;
			}
		}

		&-item{
			white-space: nowrap;
			overflow: hidden;
			text-overflow: ellipsis;
			@include pxToRem(padding, 3, 10);
			cursor: pointer;

			&.active,
			&:hover{
				//background: $clrGrey;
				color: #fff;
			}
		}
	}

	&__coordinates{

		font-size: 0;
		white-space: nowrap;

		input{
			width: (100% / 3);
			display: inline-block;
			@include pxToRem(padding, 0, 10);
			@include box-sizing();
			@include rem(line-height, 24);
			@include rem(height, 24);
			background: transparent;
			font: inherit;
			@include rem(font-size, 10);
			text-align: center;
			@include rem(border-radius, 5);

			@include transition(all $animateTime);
			@include box-shadow(inset 0 0 0 rgba(0,0,0,0.75));
			&:focus{
				background-color: $clrBorderLight;
				color: #fff;
				@include box-shadow(inset 0 0 0.6rem rgba(0,0,0,0.75));
			}

		}

		position: relative;
		&:before,
		&:after{
			content: '';
			display: block;
			position: absolute;
			@include rem(width, 2);
			@include rem(height, 12);
			@include rem(top, 6);
			background: $clrBorderLight;
		}

		&:before{
			left: (100% / 3);
		}


		&:after{
			right: (100% / 3);
		}
	}

	&__title{
		text-align: center;
	}

	&__footnote{
		text-align: right;
		@include rem(font-size, 8);

		a{
			text-decoration: underline;
		}
	}
}