@mixin absCenter(){
	position: absolute;
	top: 50%;
	left: 50%;
	@include transform(translate(-50%, -50%));
}

html {
	width:100%;
	height:100%;
	margin: 0;
	padding: 0;
	//font-size: 100.01%;
	font-size: 16px;
	min-width: $minWidth;
}

body {
	height: 100%;
	margin: 0;
	padding: 0;
	line-height: 1.2;
	font-family: 'Roboto', sans-serif;

	background: #000;
	color: $clrGrey;
}


.wrapper  {
	width: 100%;
	height: 100%;
	position: relative;
	@include rem(min-height, 740);
	overflow: hidden;
}



.checkbox{

	position: relative;
	display: inline-block;
	vertical-align: middle;
	width: 14px;
	height: 14px;

	input{
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		opacity: 1;
		z-index: 1;
		cursor: pointer;
		opacity: 0;

		&:checked{
			& ~ .checkbox__box{

				&:before{
					content: '';
					display: block;
					background: url("/img/ico_rest/checkbox.svg") no-repeat 0 0;
					background-size: contain;
					position: absolute;
					top: 0;
					left: 0;
					width: 100%;
					height: 100%;
				}
			}
		}
	}

	&__box{
		width: 10px;
		height: 10px;

		border: solid $clrBorderLight;
		@include rem(border-width, 2);
		@include rem(border-radius, 4);
	}
}



.svg{
	display: block;
	width: 100%;
	height: 100%;

	polygon,path{
		fill: currentColor;
	}
}

.game {
	position: relative;
	width: 100%;
	height: 100%;
	@include box-sizing();
	@include rem(padding-top, 30);





	&__interface{
		height: 100%;
		width: 100%;
		position: relative;
	}






	&__galaxy{
		position: absolute;
		top:0;
		left: 0;
		width: 100%;
		height: 100%;
		//background: url("/img/bg.jpg") no-repeat 50% 50%;
		background-size: cover;

		canvas{
			display: block;
		}

		&-star{
			position: absolute;
			@include rem(width, 1);
			@include rem(height, 1);
			background: #eaeace;
			@include box-shadow(0 0 0.312rem 0.0625rem rgba(234, 233, 36, 0.4));
			@include border-radius(50%);
			@include transition(all 0.8s ease);


			&.is-lightning{
				background: #fff;
				@include box-shadow(0 0 0.612rem 0.15rem rgba(234, 233, 200, 1));
				opacity: 1 !important;
			}
		}
	}

	@include transition(left 0.3s ease);
	left: 0;





	&__planet{
		@include rem(width, 620);
		@include rem(height, 620);
		margin: 0 auto;

		@include absCenter();

		&-name{
			width: 100%;
			text-align: center;
			position: absolute;
			@include rem(top, -40);
			left: 0;

			.val{
				display: inline-block;
				vertical-align: middle;
				@include rem(font-size, 19);
				font-weight: 900;
				color: #fff;
				@include rem(margin-right, 5);
			}

			.ico{
				@include rem(margin, 0, 2);
			}
		}

		&-view{
			@include rem(width, 620);
			@include rem(height, 620);

			@include absCenter();

			img{
				width: 100%;
				height: 100%;
			}
		}

		&-desk{
			@include rem(width, 500);
			@include rem(height, 290);
			background: url("/img/planet_desk.svg") no-repeat 50% 50%;
			background-size: contain;

			@include absCenter();
		}


		&-more{
			position: absolute;
			top: 100%;
			width: 100%;
			text-align: center;
			left: 0;
			@include rem(margin-top, 8);
			font-size: 0;

			.btn{
				display: inline-block;
				background: #1e2435;
				@include rem(height, 30);
				@include rem(line-height, 30);
				@include rem(padding, 0, 19, 0, 23);
				@include rem(margin, 0, 5);
				@include rem(border-radius, 4);

				i, span{
					color: #fff;
					opacity: 0.4;
					vertical-align: middle;
					@include rem(font-size, 13);
					display: inline-block;
				}

				i{
					@include rem(margin-left, 7);
				}

				&:hover{
					background: #212b42;

					i, span{
						opacity: 1;
					}
				}
			}
		}
	}




	&__history{
		position: absolute;
		@include rem(top, 20);
		@include rem(left, 45);
		@include rem(width, 66);



		&-item{
			background: rgba(0,0,0,0.6);
			border: solid $clrBorder;
			@include rem(border-width, 2);
			@include rem(border-radius, 7);
			position: relative;
			@include rem(margin-bottom, 8);
			@include rem(width, 58);
			float: left;
			display: block;
			@include rem(font-size, 10);
			@include rem(margin-right, 4);

			&_message{

				@include rem(min-height, 48);
				@include rem(max-height, 48);
				overflow: hidden;
				@include transition(all $animateTime);

				&.is-open{
					@include rem(width, 292);
					max-height: 50vh;
				}
			}

			&_btn{
				@include rem(height, 22);
				@include rem(line-height, 22);
				text-align: center;
				color: #4e5261;
				@include transition(all $animateTime);

				&:hover{
					color: #fff;

					.ico{
						background-position: 100% 0;
					}
				}

				&_half{
					@include rem(width, 25);
				}
			}

		}

		&-status{
			position: absolute;
			@include rem(top, 20);
			@include rem(left, 5);
			@include rem(width, 8);
			@include rem(height, 8);

			background: url("/img/ico_status/ico-status_norm.svg") no-repeat 50% 50%;
			background-size: cover;

			.game__history-item_good &{
				background-image: url("/img/ico_status/ico-status_good.svg")
			}

			.game__history-item_bad &{
				background-image: url("/img/ico_status/ico-status_bad.svg")
			}
		}

		&-ico{
			position: absolute;
			@include rem(top, 8);
			@include rem(left, 18);
			@include rem(width, 32);
			@include rem(height, 32);

			img{
				opacity: 0.5;
				filter: sepia() saturate(1000%) hue-rotate(-185deg);
				@include transition(all $animateTime);

				.game__history-item.is-open &,
				.game__history-item:hover &{
					opacity: 1;
					filter: none;
				}
			}
		}


		&-info{
			@include rem(width, 200);
			@include rem(padding-right, 30);
			@include rem(padding-left, 58);
			position: relative;
			opacity: 0;
			overflow: hidden;
			@include rem(min-height, 48);

			@include transition(opacity $animateTime);


			.is-open &{
				opacity: 1;
			}
		}

		&-tag{
			@include rem(margin, 8, 0);

			.tag{
				display: inline-block;
				border: solid $clrBorderLight;
				@include rem(font-size, 8);
				@include rem(border-width, 1);
				@include rem(height, 14);
				@include rem(line-height, 14);
				@include rem(padding, 0, 4);
				vertical-align: middle;

				@include rem(margin-right, 5);
				@include rem(border-radius, 3);
				cursor: default;
				font-weight: 600;

				i{
					font: inherit;
				}

				&_time{
					background: $clrYellow;
					color: #000;

					.game__history-item_good &{
						background: $clrGreen;
					}

					.game__history-item_bad &{
						background: $clrRed;
					}
				}
			}
		}

		&-title{
			@include rem(font-size, 11);
			font-weight: 500;
			color: #fff;
			@include rem(margin, 8, 0);

			.game__history-item_good &{
				color: $clrGreen;
			}

			.game__history-item_bad &{
				color: $clrRed;
			}
		}
		&-text{
			@include rem(font-size, 10);
			@include rem(margin, 8, 0);

			.ico{
				@include rem(width, 16);
				@include rem(height, 16);
			}

			.game__history-item_btn &{
				margin: 0;
			}
		}

		&-val{
			font-size: 0;
		}

		&-close{
			position: absolute;
			@include rem(width, 16);
			@include rem(height, 16);
			@include rem(top, 8);
			@include rem(right, 8);
			cursor: pointer;
		}

		&-open{
			position: absolute;
			@include rem(width, 16);
			@include rem(height, 16);
			@include rem(bottom, 8);
			@include rem(right, 8);
			cursor: pointer;
		}
	}














	&__menu{
		position: absolute;
		@include rem(bottom, 75);
		@include rem(left, 45);
		background: rgba(0,0,0,0.6);
		@include rem(padding, 9, 15);
		border: solid $clrBorder;
		@include rem(border-width, 2);
		@include rem(border-radius, 7);

		&-title{
			display: inline-block;
			vertical-align: middle;
			@include rem(font-size, 10);
		}

		&-item{
			display: inline-block;
			vertical-align: middle;
			@include rem(width, 20);
			@include rem(height, 20);
			@include rem(margin-left, 5);
			cursor: pointer;

			img{
				display: block;
				width: 100%;
				height: 100%;
			}

			opacity: 0.5;
			filter: sepia() saturate(1000%) hue-rotate(-185deg);
			@include transition(all $animateTime);

			&:hover {
				opacity: 1;
				filter: none;
			}
		}
	}











	$timerWidth: 42;
	$rocketWidth: 26;
	$deltaPath: ($timerWidth + $rocketWidth);

	&__attack{
		position: absolute;
		@include rem(top, 20);
		@include rem(right, 45);



		&-close{
			position: absolute;
			@include rem(width, 16);
			@include rem(height, 16);
			@include rem(top, 8);
			@include rem(right, 8);
			cursor: pointer;
		}

		&-open{
			position: absolute;
			@include rem(width, 16);
			@include rem(height, 16);
			@include rem(bottom, 8);
			@include rem(right, 8);
			cursor: pointer;
		}


		&-item{
			background: rgba(0,0,0,0.6);
			border: solid $clrBorder;
			@include rem(border-width, 2);
			@include rem(border-radius, 7);
			float: right;
			display: block;
			clear: both;
			@include user-select();
			@include rem(margin-bottom, 8);

			@include rem(width, 112);
			@include rem(max-height, 22);
			overflow: hidden;
			cursor: pointer;
			@include transition(all $animateTime);

			&.is-open{
				cursor: default;
				@include rem(max-height, 80);
				@include rem(width, 284);
			}
		}

		&-text{
			@include rem(line-height, 22);
			@include rem(height, 22);
			@include rem(font-size, 10);
			@include rem(padding, 0, 10);
			white-space: nowrap;
			overflow: hidden;
			text-overflow: ellipsis;
			text-align: center;

			@include transition(color $animateTime);
			color: $clrGrey;


			.game__attack-item:hover &{
				color: #fff;
			}

		}

		&-preview{
			position: absolute;
			@include transition(opacity $animateTime);


			.game__attack-item_back &{
				opacity: 0.3;
			}

			.is-open &{
				opacity: 0;
			}


			&-ico{
				@include rem(width, 14);
				@include rem(height, 14);
				position: absolute;
				@include rem(top, 4);

				.game__attack-item_attack &{
					color: $clrYellow;
					@include rem(left, 10);
				}

				.game__attack-item_protect &{
					color: $clrRed;
					@include rem(left, 90);
				}

				svg{
					display: block;
					height: 100%;
					width: 100%;

					path{

						fill: currentColor;

					}
				}
			}

			&-rocket{
				@include rem(width, 26);
				@include rem(height, 14);
				position: absolute;
				@include rem(top, 4);


				.game__attack-item_attack &{
					color: $clrYellow;
					@include rem(left, 80);
				}

				.game__attack-item_protect &{
					@include transform(rotate(180deg));
					color: $clrRed;
					@include rem(left, 7);
				}

				svg{
					display: block;
					height: 100%;
					width: 100%;

					path{
						fill: currentColor;
					}
				}
			}

			&-timer{
				@include rem(width, $timerWidth);
				@include rem(height, 14);
				@include rem(line-height, 14);
				@include rem(border-radius, 2);
				text-align: center;
				position: absolute;
				@include rem(top, 4);
				@include rem(left, 0);

				background: $clrGrey;
				color: #000;
				@include rem(font-size, 8);
				font-weight: 600;


				.game__attack-item_attack &{
					background: $clrYellow;
					@include rem(left, 30);
				}

				.game__attack-item_protect &{
					background: $clrRed;
					@include rem(left, 40);
				}

			}
		}

		&-inner{
			position: relative;
			@include rem(font-size, 10);
			@include rem(width, 225);
			@include rem(padding, 9, 35, 9, 20);
			opacity: 0;
			@include transition(opacity $animateTime);

			.is-open &{
				opacity: 1;
			}
		}


		&-head{

			overflow: hidden;
			opacity: 1;

			&-title{
				float: left;
				@include rem(line-height, 16);
				white-space: nowrap;
				overflow: hidden;
				text-overflow: ellipsis;
				@include rem(width, 70);
			}

			&-planet{
				float: right;
				@include rem(line-height, 16);

				span{
					@include rem(line-height, 16);
					display: inline-block;
					vertical-align: middle;
					white-space: nowrap;
					overflow: hidden;
					text-overflow: ellipsis;
					@include rem(max-width, 110);
				}

				.ico{
					@include rem(width, 16);
					@include rem(height, 16);
					@include rem(margin-left, 4);
				}
			}
		}

		&-action{

			position: relative;

			&-row{
				overflow: hidden;

				&_forward{

					.game__attack-item_attack &{
						color: $clrYellow;
					}

					.game__attack-item_protect &{
						color: $clrRed;
					}

					.game__attack-item_back &{
						color: $clrBorder;
					}
				}

				&_back{

				}
			}

			&-title{
				@include rem(height, 22);
				@include rem(line-height, 22);
				width: 25%;

				.game__attack-item_attack &{
					float: left;
				}

				.game__attack-item_protect &{
					float: right;
					text-align: right;
				}
			}

			&-line{
				width: 75%;
				@include rem(height, 4);
				@include rem(margin, 9, 0);
				background: $clrBorder;
				@include rem(line-height, 22);
				position: relative;

				&:before{
					content: '';
					display: block;
					@include rem(width, 4);
					@include rem(height, 4);
					border-radius: 50%;
					top: 0;
					position: absolute;
					@include box-shadow(0 0 0.5rem currentColor);

					@include animation(blink 2s linear infinite);
				}

				.game__attack-item_attack &{
					float: right;

					&:before{
						right: 0;
						color: $clrYellow;
						background: $clrYellow;
					}
				}

				.game__attack-item_protect &{
					float: left;

					&:before{
						left: 0;
						color: $clrRed;
						background: $clrRed;
					}
				}

				.game__attack-action-row_back &{
					&:before{
						display: none;
					}
				}


				.game__attack-item_attack.game__attack-item_back &,
				.game__attack-item_protect.game__attack-item_back &{
					&:before{
						display: none;
					}
				}
			}
		}

		&-progress{

			position: absolute;
			left: 0;
			right: 0;
			top: 0;

			.game__attack-item_back &{
				@include rem(top, 22);
			}

			.game__attack-item_attack &{
				left: 25%;
			}

			.game__attack-item_protect &{
				right: 25%;
			}


			&-line{
				position: absolute;
				@include rem(height, 4);
				@include rem(top, 9);
				right: 0;
				left: 0;

				.game__attack-item_attack &{
					@include rem(left, $deltaPath);
				}

				.game__attack-item_protect &{
					@include rem(right, $deltaPath);
				}

				.game__attack-item_back &{

				}


				.game__attack-item_attack.game__attack-item_back &{
					left: 0;
					@include rem(right, $deltaPath);
				}

				.game__attack-item_protect.game__attack-item_back &{
					right: 0;
					@include rem(left, $deltaPath);
				}
			}

			&-path{
				position: absolute;
				top: 0;
				height: 100%;
				max-width: 100%;

				&:before{
					content: '';
					position: absolute;
					display: block;
					top: 0;
					height: 100%;
					@include rem(width, $deltaPath);
					background: inherit;
				}

				&:after{
					content: '';
					position: absolute;
					display: block;
					top: 0;
					height: 100%;
					@include rem(width, $rocketWidth);
					background: $clrBorder;
				}

				.game__attack-item_attack &{
					background: $clrYellow;
					left: 0;

					&:before{
						@include rem(left, - $deltaPath);
					}

					&:after{
						right: 0;
					}
				}

				.game__attack-item_protect &{
					background: $clrRed;
					right: 0;

					&:before{
						@include rem(right, - $deltaPath);
					}

					&:after{
						left: 0;
					}
				}

				.game__attack-item_back &{
					background: $clrGrey;
				}

				.game__attack-item_attack.game__attack-item_back &{
					left: auto;
					right: 0;

					&:before{
						left: auto;
						@include rem(right, - $deltaPath);
					}

					&:after{
						right: auto;
						left: 0;
					}
				}

				.game__attack-item_protect.game__attack-item_back &{
					right: auto;
					left: 0;

					&:before{
						left: 0;
						@include rem(left, - $deltaPath);
					}

					&:after{
						left: auto;
						right: 0;
					}
				}

			}

			&-rocket{
				@include rem(width, 26);
				@include rem(height, 14);
				position: absolute;
				@include rem(top, -5);
				z-index: 1;

				svg{
					width: 100%;
					height: 100%;

					path{


						.game__attack-item_attack &{
							fill: $clrYellow;
						}

						.game__attack-item_protect &{
							fill: $clrRed;
						}

						.game__attack-item_back &{
							fill: $clrGrey;
						}

						.game__attack-item_attack.game__attack-item_back &{
						}

						.game__attack-item_protect.game__attack-item_back &{
						}
					}
				}

				.game__attack-item_attack &{
					right: 0;
				}

				.game__attack-item_protect &{
					left: 0;
					@include transform(rotate(180deg));
				}

				.game__attack-item_attack.game__attack-item_back &{
					right: auto;
					left: 0;
					@include transform(rotate(180deg));
				}

				.game__attack-item_protect.game__attack-item_back &{
					left: auto;
					right: 0;
					@include transform(rotate(0deg));
				}
			}

			&-timer{
				@include rem(width, $timerWidth);
				@include rem(height, 14);
				@include rem(line-height, 14);
				@include rem(border-radius, 2);
				text-align: center;
				position: absolute;
				@include rem(top, 4);

				background: $clrGrey;
				color: #000;
				@include rem(font-size, 8);
				font-weight: 600;

				.game__attack-item_attack &{
					background: $clrYellow;
					left: 0;
				}

				.game__attack-item_protect &{
					background: $clrRed;
					right: 0;
				}

				.game__attack-item_back &{
					background: $clrGrey;
				}


				.game__attack-item_attack.game__attack-item_back &{
					left: auto;
					right: 0;
				}

				.game__attack-item_protect.game__attack-item_back &{
					right: auto;
					left: 0;
				}
			}
		}



	}














	&__satellite{

		position: absolute;
		@include rem(right, 45);
		@include rem(bottom, 75);
		font-size: 0;
		@include rem(width, 340);
		text-align: right;

		&-item{
			@include rem(width, 64);
			@include rem(height, 64);
			position: relative;
			display: inline-block;
			@include rem(margin, 20, 0, 0, 20);
			@include transition(opacity $animateTime);

			@include user-select();

		}

		&-planet{
			position: absolute;
			width: 100%;
			height: 100%;
			border-radius: 50%;
			border: transparent solid;
			@include rem(border-width, 2);
			@include transition(all $animateTime);
			opacity: 1;

			img{
				width: 100%;
				height: 100%;
				@include transition(opacity $animateTime);

				.in-hover &{
					opacity: 0.3;
				}
				.is-hover &{
					opacity: 1;
				}
			}

			.in-hover &{
				border-color: $clrBorder;
			}
			.is-hover &{
				border-color: transparent;
			}

		}

		&-info{
			position: absolute;
			@include rem(font-size, 10);
			@include rem(width, 140);
			@include rem(padding-right, 0);
			@include transition(all $animateTime);
			@include box-sizing();
			right: 0;
			width: 0;
			overflow: hidden;
			opacity: 0;

			.is-hover &{
				@include rem(width, 215);
				opacity: 1;
			}
		}

		&-title{
			color: #fff;
			@include rem(font-size, 11);
			@include rem(margin-bottom, 5);
			@include rem(width, 140);

			span{
				vertical-align: middle;
				display: inline-block;
			}

			.ico{
				@include rem(margin-left, 3);
			}
		}

		&-text{
			@include rem(width, 140);
		}



		&-attaked{
			position: absolute;
			@include pxToRem(top, -4);
			@include pxToRem(left, -4);
			@include box-sizing();
			@include rem(width, 75);
			@include rem(height, 75);
			border: solid rgba($colorRed, 0.5);
			@include rem(border-width, 1);
			border-radius: 50%;
			@include transition(all $animateTime);

			&:after{
				content: '';
				@include rem(width, 14);
				@include rem(height, 14);
				position: absolute;
				@include rem(bottom, -7);
				left: 50%;
				@include rem(margin-left, -7);
				display: block;
				opacity: 0;
				@include transition(opacity $animateTime);
				background: $colorRed url("/img/ico_rest/ico-sword.svg") no-repeat 50% 50%;
				background-size: 65% auto;
				border-radius: 50%;
			}

			a{
				display: block;
				position: absolute;
				left: 50%;
				top: 50%;
				@include rem(width, 30);
				@include rem(height, 30);
				@include rem(margin-left, -15);
				@include rem(margin-top, -5);
				background: url("/img/ico_rest/arrow-down.svg") no-repeat 50% 50%;
				background-size: contain;
				@include transition(all $animateTime);
				opacity: 0;

				.is-hover &{
					@include rem(margin-top, 5);
					opacity: 1;
					z-index: 1;
				}

			}

			.in-hover &{
				border-color: rgba($colorRed, 0);
			}
			.is-hover &{
				border-color: rgba($colorRed, 1);
				@include rem(border-width, 2);

				&:after{
					opacity: 1;
				}
			}
		}
	}





}



@include keyframes(blink){
	0% { opacity: 0; }
	50% { opacity: 1; }
	100% { opacity: 0; }
}



.game__content{
	position: relative;
	width: 100%;
	height: 100%;
}



.timer{
	display: inline-block;
	text-align: center;
	@include rem(height, 14);
	@include rem(line-height, 14);
	@include rem(border-radius, 2);
	@include rem(padding, 0, 5);
	background: $clrGrey;
	color: #000;
	@include rem(font-size, 8);
	font-weight: 600;
}