.aside{
	background: #222328;
	position: absolute;
	top:0;
	height: 100%;
	@include rem(width, 300);
	@include box-sizing();
	@include rem(padding, 30, 0);
	@include rem(left, -300);
	@include transition(left 0.3s ease);


	&__logo{
		@include rem(height, 100);
		@include rem(margin, 0, 15, 15);
		background: #2d2f35;
	}

	&__menu{

		@include rem(height, 670);
		&-item{
			&:not(:first-child){
				border-top: solid #0d0d0f;
				@include rem(border-width, 2);
			}


			&_submenu{

				.aside__menu-link{
					position: relative;

					&:before,
					&:after{
						content: '';
						position: absolute;
						@include rem(width, 10);
						@include rem(height, 2);
						display: block;
						background: $colorBg;
						@include border-radius(10px);
						@include rem(right, 50);
						top:50%;
						@include rem(margin-top, -1);
						@include transition();
					}

					&:before{
						@include rem(margin-right, -1);
						@include transform(rotate(45deg));
					}

					&:after{
						@include rem(margin-right, -7);
						@include transform(rotate(-45deg));
					}
				}

				&.is-open{
					.aside__menu-link{
						&:before{
							@include transform(rotate(-45deg));
						}

						&:after{
							@include transform(rotate(45deg));
						}
					}
				}

			}
		}
		&-link{
			@include rem(font-size, 11);
			color: #b0cad8;
			display: block;
			@include rem(height, 26);
			@include rem(padding, 10, 15);
			cursor: pointer;

			&:hover{
				color: #fff;
			}
		}
		&-ico{
			@include rem(width, 26);
			@include rem(height, 26);
			float: left;
		}
		&-name{
			@include rem(margin-left, 40);
			@include rem(margin-top, 6);

			span{
				display: inline-block;
				@include rem(padding, 0, 8);
				@include rem(border-radius, 4);
				@include rem(height, 20);
				@include rem(line-height, 20);
				@include rem(font-size, 10);
				color: #fff;
			}
		}
	}


	&__submenu{
		display: none;
		@include rem(padding-bottom, 15);
		@include rem(font-size, 10);

		&-item{

		}

		&-link{
			color: #b0cad8;
			display: block;
			cursor: pointer;
			@include rem(padding, 5, 15, 5, 60);

			&:hover{
				color: #fff;
			}
		}
	}

	&_open{
		left: 0;

		& ~ {
			.header{
				@include rem(left, 300);

				.header__label{
					.ico{
						@include rem(width, 2);
						@include rem(height, 2);
						@include rem(margin-left, 7);
						@include rem(margin-right, 7);

						&:before{
							@include rem(top, 1);
							@include transform(rotate(135deg))
						}

						&:after{
							@include rem(top, 1);
							@include transform(rotate(45deg))
						}
					}
				}
			}

			.game{
				@include rem(left, 300);
			}
		}

	}
}