
.modal{
	color: $clrGrey;

	.popup{

		&__layout{
			box-sizing: border-box;
			display: inline-block;
			margin: 0 auto;
			padding: 0;
			position: relative;
			vertical-align: middle;
			white-space: normal;
			@include rem(width, 178);
			z-index: 102;
			text-align: left;


			border: $clrBorderLight solid;
			@include rem(border-width, 2);
			@include rem(border-radius, 7);
			background: $clrDark;
		}






	}

	.mCSB_container{

	}

	.mCSB_vertical{
		.mCSB{
			&_scrollTools{

				@include pxToRem(right, 3);

				.mCSB_dragger_bar{
					@include pxToRem(width, 5);
					background: rgba($colorText, 0.20);
				}
			}
			&_draggerContainer{
				@include pxToRem(top, 25);
				@include pxToRem(bottom, 8);
			}

		}
	}

	.mCSB_horizontal{
		.mCSB{
			&_scrollTools{

				@include pxToRem(bottom, 25);


				.mCSB_dragger_bar{
					@include pxToRem(height, 10);
					background: rgba($colorText, 0.20);
				}
			}
			&_draggerContainer{
				@include pxToRem(left, 20);
				@include pxToRem(right, 20);
			}

		}
	}

	&__content{
		@include rem(padding, 5, 17);
	}

	&__title{
		@include rem(font-size, 14);
		font-weight: 500;
		color: #fff;
		text-align: center;
		@include rem(margin-bottom, 2);
	}

	&__subtitle{
		@include rem(font-size, 11);
		text-align: center;
		color: #fff;
		@include rem(margin-bottom, 2);
	}

	&__timer{
		text-align: center;

		.timer{
			background: $clrRed;
			@include rem(font-size, 10);
			@include rem(margin-top, 10);
		}

	}

	&__text{
		@include rem(font-size, 10);
		text-align: center;
		@include rem(margin-bottom, 2);
	}


	&__head{
		@include rem(margin, 15, 0);
	}

	&__form{
		@include rem(margin, 15, 0);
	}

	&__btn{
		line-height: 1;
		@include rem(padding, 10);
		@include rem(font-size, 11);
		color: #fff;
		text-align: center;
		border: $clrBorderLight solid;
		@include rem(border-width, 2);
		@include rem(border-radius, 0 , 0, 7, 7);
		@include rem(margin, 0, -2, -2, -2);
		display: block;
		background: $clrBorder;

		&_red{
			background: $clrRed;
			border-color: $colorRedLight;

			&:hover{
				background: $colorRedLight;
				border-color: $clrRed;
			}
		}

		&_violet{
			background: $clrViolet;
			border-color: $clrVioletLight;

			&:hover{
				background: $clrVioletLight;
				border-color: $clrViolet;
			}
		}

		&_disable{
			cursor: default;
			background: $clrBorder!important;
			border-color: $clrBorderLight!important;
		}
	}



	&__generate{
		text-align: center;
		@include rem(margin, 15, 0);

		&-btn{
			@include rem(width, 24);
			@include rem(height, 24);
			border: $clrBorderLight solid;
			@include rem(border-width, 2);
			border-radius: 50%;
			background: url("/img/ico_rest/ico-reload.svg") no-repeat 50% 50%;
			background-size: contain;
			margin: 0 auto;
			cursor: pointer;
		}

		&-title{
			@include rem(margin-top, 2);
		}
	}

	&__footnote{
		@include rem(margin, 15, 0);
		text-align: center;
		@include rem(font-size, 8);

		a{
			text-decoration: underline;
		}
	}


	&__alert{
		@include rem(margin, -5, 0);
		text-align: center;
		color: $clrRed;

		&-ico{
			@include rem(margin-bottom, 2);
		}
	}

	&__planet{
		@include rem(margin, 15, 0);
		text-align: center;

		&-out{
			margin: 0 auto;
			@include rem(width, 75);
			@include rem(height, 75);
			@include rem(padding, 4);
			border: $clrBorderLight solid;
			@include rem(border-width, 2);
			@include box-sizing();
			border-radius: 50%;
			@include rem(margin-bottom, 25);
			position: relative;

			img{
				width: 100%;
				height: 100%;
			}
		}

		&-arrow{

			position: absolute;
			@include rem(width, 2);
			@include rem(height, 20);
			background: $clrBorderLight;
			@include rem(top, 73);
			left: 50%;
			@include rem(margin-left, -1);
			@include rem(border-radius, 100);

			&:before,
			&:after{
				content: '';
				display: block;
				@include rem(border-radius, 100);
				@include rem(width, 2);
				@include rem(height, 6);
				background: $clrBorderLight;
				position: absolute;
				bottom: 0;
				left: 50%;
			}

			&:before{
				@include transform(rotate(-40deg));
				@include rem(margin-left, -3);

			}

			&:after{
				@include transform(rotate(40deg));
				@include rem(margin-left, 1);
			}
		}

		&-text{
			@include rem(margin, 8, 0);
		}

		&-in{
			margin: 0 auto;
			@include rem(width, 35);
			@include rem(height, 35);
			@include rem(padding, 4);
			border: $clrBorderLight solid;
			@include rem(border-width, 2);
			border-radius: 50%;
			@include box-sizing();

			img{
				width: 100%;
				height: 100%;
			}

			&.is-scan{
				position: relative;


				&:before{
					content: '';
					display: block;
					position: absolute;
					@include rem(top, 4);
					@include rem(left, 4);
					@include rem(right, 4);
					@include rem(bottom, 4);

					background: url("/img/ico_rest/ico-planet-load.png") no-repeat 50% 50%;
					background-size: contain;
					@include animation(rotation 2s linear infinite);
				}
			}
		}
	}

	&__info{
		@include rem(margin, 15, 0);

		.modal__text{
			@include rem(margin, 8, 0);
		}
	}

	.donut{
		@include rem(height, 18);
		@include rem(line-height, 18);
		@include rem(padding, 0, 6);
		border: $clrBorderLight solid;
		@include rem(border-width, 1);
		display: inline-block;
		@include rem(border-radius, 3);
		font-size: 0;

		.ico{

		}
		.val{
			display: inline-block;
			vertical-align: middle;
			@include rem(font-size,10);
			@include rem(margin, 0, 5);
		}
	}



	&__list{

		&-item{
			display: table;
			width: 100%;

			&.is-attack{
				position: relative;
				color: #fff;

				&:before{
					content: '';
					display: block;
					@include rem(width, 4);
					@include rem(height, 4);
					@include rem(top, 9);
					@include rem(left, 9);
					border-radius: 50%;
					background: $clrRed;
					position: absolute;
					@include box-shadow(0 0 1rem $clrRed);
				}
			}
		}

		&-head{
			display: table;
			width: 100%;
			@include rem(font-size, 11);
			color: rgba(#fff, 0.5);
			@include rem(margin-bottom, 5);
		}

		&-name{
			display: table-cell;
			@include rem(padding, 5, 10, 2, 20);
		}

		&-val{
			display: table-cell;
			text-align: right;
			@include rem(padding, 5, 20, 2, 10);
		}
	}









	&_list{
		.modal__inner{
			display: flex;
			flex-direction: column;
			@include rem(height, 300);
		}

		.modal__list{
			flex: 1 1 auto;
			overflow: hidden;
			min-height: 0;
		}

		.modal__info{
			margin: 0;
			border-top: $clrBorderLight solid;
			@include rem(border-width, 2);
		}

		.modal__text{
			span{
				display: inline-block;
				vertical-align: middle;
			}
		}

		.donut{
			@include rem(margin-left, 10);
		}
	}
}




@keyframes rotation {
	from {
		@include transform(rotate(0deg));
	}
	to {
		@include transform(rotate(360deg));
	}
}